/**
 * デジタルバインダー
 */
import util from './util';
import { calcStatAll } from './statistics';
import CONFIG from './config';
import { makeRequestUrl } from './util/common';

import jsQR from 'jsqr';

import { Card, AikatsuVersion } from '../types/common';
import { msgAlert } from './util/view';

// QR登録のプログレスバー
let qrCounter = 0;
/** QRコードリーダのインスタンスオブジェクト */
let qrReaderIntervalId: number;

export function qrRegistInit() {
  // インポートボタン
  $(document).on('click', '#qrimp', () => {
    qrImportButtonClick();
  });

  // QRエクスポートボタン
  $(document).on('click', '#qrexp', () => {
    $('#imp_text').val('');
    $('#qr_result').empty();

    let list = '';
    for (const card of window.RGList_v2) {
      if (card.dressId && card.additional) {
        list = `${list}${card.cardId},${card.cardName},${card.qrcode},${card.dressId},${card.additional}\n`;
      } else if (card.dressId) {
        list = `${list}${card.cardId},${card.cardName},${card.qrcode},${card.dressId}\n`;
      } else {
        list = `${list}${card.cardId},${card.cardName},${card.qrcode}\n`;
      }
    }
    $('#qr_result').val(list);
  });

  // QR画面のQRインポート結果・エクスポート結果表示エリア
  $(document).on('click', '#qr_result', e => {
    // クリックしたら全選択する
    e.target.setSelectionRange(0, e.target.value.length);
  });

  // QR画面、その他QRの閉じるボタン
  $(document).on('click', '#close', () => {
    $('#QR_box').fadeOut();
    $('#QR_mynumber').fadeOut();
  });

  // メニュー：QRコード登録画面表示
  $('.m_btn-qread').on('click', () => {
    $('.RegQR').text(window.RGList_v2.length);
    util.nav();
    $('#qr_result').val('');
    $('#QR_box').fadeIn();
  });

  // QRコードリーダ起動ボタン
  $(document).on('click', '#qrReaderButton', () => startRecogQR());
  $(document).on('change', '#qrReaderDeviceSelect', () => startRecogQR());
  // QRコードリーダ停止
  $(document).on('click', '#qrReaderStop', () => stopRecogQR());
}

/**
 * QR登録画面で、インポートボタンを押下した時の処理
 */
async function qrImportButtonClick() {
  // QR登録のテキストエリアの内容。空の行は削除する
  const qrAry = ($('#imp_text').val() as string).split(/\r\n|\r|\n/);
  const qrList = extractImportQr(qrAry);
  console.log(qrList);

  const errorMessage = qrList.errors.join('\n');
  $('#qr_result').val(errorMessage);

  // 登録対象のQRコードが存在しない場合は終了
  if (qrList.data.length === 0) {
    util.msgAlert('新規登録可能なQRコードが入力されていません。');
    return;
  }

  // カウンターを初期化
  qrCounter = 0;
  $('.bar').text('0%');
  $('.bar').css('width', '0%');
  $('.indicator').show(); // プログレスバーを表示

  for (const qrImportInfo of qrList.data) {
    await import_qr(qrImportInfo.accessUrl, qrImportInfo.qrcode, qrList.data.length, qrImportInfo.dressId, qrImportInfo.additional, qrImportInfo.aikatsuVer);
  }
}

/**
 * QR登録欄の入力テキストから、登録用に整形する
 * @param inputText ユーザ入力のテキスト。行単位の配列
 * @return 整形したやつ
 */
function extractImportQr(inputQrList: string[]) {
  // アイカツのQRコードが書かれた行のみに絞り込む
  inputQrList = inputQrList.filter(inputText => {
    return inputText.indexOf('dcd.sc') !== -1;
  });

  const data = [];
  const errors = [];

  for (const inputQr of inputQrList) {
    // 1行の入力欄を解釈し、ID、Name、QR、deree_idを取得する
    const qrImportInfo: {
      dressId: string | null;
      additional: string | null;
      qrcode: string;
      aikatsuVer: AikatsuVersion;
      accessUrl: string;
    } = {
      dressId: null,
      additional: null,
      qrcode: '',
      aikatsuVer: 'stars',
      accessUrl: ''
    };

    const importInfo = inputQr.split(',');

    switch (importInfo.length) {
      case 1:
        // QRのみ
        qrImportInfo.qrcode = importInfo[0];
        break;
      case 2:
        // 取りえない形式
        errors.push(`入力形式が正しくありません:${inputQr}`);
        break;
      case 3:
        // ID, カード名, QR
        qrImportInfo.qrcode = importInfo[2];
        break;
      case 4:
        // ID, カード名, QR, ドレスID
        qrImportInfo.qrcode = importInfo[2];
        qrImportInfo.dressId = importInfo[3];
        break;
      case 5:
        // ID, カード名, QR, ドレスID, 付帯情報
        qrImportInfo.qrcode = importInfo[2];
        qrImportInfo.dressId = importInfo[3];
        qrImportInfo.additional = importInfo[4];
        break;
    }

    // 登録済みQRと合致するものを探し、合致したらインポート対象から除外する
    const cardList = util.grepcards('qrcode', qrImportInfo.qrcode);
    if (cardList.length !== 0) {
      errors.push(`登録済:${inputQr}`);
      continue;
    }

    // スターズ、またはフレンズのドレスIDの形式をチェック
    const dressId = qrImportInfo.dressId;
    if (dressId !== null && !dressId.match(/.*_.*_.*_.*_.*_.*/) && !dressId.match(/\d/)) {
      errors.push(`ドレスIDの形式が正しくありません:${inputQr}`);
      continue;
    }

    // QRコードの形式チェックして84文字でない場合スキップ
    if (qrImportInfo.qrcode.length !== 84) {
      errors.push(`QRコードの形式が正しくありません:${inputQr}`);
      continue;
    }

    // QRコードのタイプ(j0,j2)をチェックする。ドレスIDの指定を要するかもチェックする
    let isNeedDressId;
    switch (true) {
      case /http:\/\/dcd.sc\/j0/.test(qrImportInfo.qrcode):
        isNeedDressId = true;
        qrImportInfo.aikatsuVer = 'stars';
        break;
      case /http:\/\/dcd.sc\/j2/.test(qrImportInfo.qrcode):
        isNeedDressId = false;
        qrImportInfo.aikatsuVer = 'stars';
        break;
      case /http:\/\/dcd.sc\/n0/.test(qrImportInfo.qrcode):
        isNeedDressId = true;
        qrImportInfo.aikatsuVer = 'friends';
        break;
      case /http:\/\/dcd.sc\/n2/.test(qrImportInfo.qrcode):
        isNeedDressId = false;
        qrImportInfo.aikatsuVer = 'friends';
        break;
      default:
        errors.push(`非対応のQRコード形式です:${inputQr}`);
        continue;
    }

    // j0プロモかつdressIdなしの場合スキップ
    if (isNeedDressId && qrImportInfo.dressId === null) {
      errors.push(`このカードはプロモQR登録でインポートしてください:${inputQr}`);
      continue;
    }
    // ドレスIDが数字だけのものは、フレンズかオンパレ
    if (qrImportInfo.dressId && qrImportInfo.dressId.match(/\d+/)) {
      // フレンズの一部のカードはIDがオンパレの領域に入ってる
      if (
        ['146917', '146918', '146919', '146920', '146921', '146922', '146923', '146924', '146925', '146926', '146927', '146928', '146929'].includes(qrImportInfo.dressId) ||
        Number(qrImportInfo.dressId) < 146690
      ) {
        qrImportInfo.aikatsuVer = 'friends';
      } else if (Number(qrImportInfo.dressId) >= 146690) {
        qrImportInfo.aikatsuVer = 'onparade';
      }
    }

    if (qrImportInfo.dressId) {
      // アクセス先のURLを決定
      switch (qrImportInfo.aikatsuVer) {
        case 'stars':
          qrImportInfo.accessUrl = `${CONFIG.url.aist}cardlist/dresslist_detail.php?dress_key=${qrImportInfo.dressId}`;
          break;
        case 'friends':
          qrImportInfo.accessUrl = `${CONFIG.url.aifr}cardlist/detail.php?id=${qrImportInfo.dressId}`;
          break;
        case 'onparade':
          qrImportInfo.accessUrl = `${CONFIG.url.aiop}cardlist/detail.php?id=${qrImportInfo.dressId}`;
      }
    } else {
      qrImportInfo.accessUrl = qrImportInfo.qrcode;
    }

    // 特にチェックに引っかからなかったものをインポート対象とする
    if (errors.indexOf(inputQr) === -1) data.push(qrImportInfo);
  }

  return { data, errors };
}

/**
 * QRインポート処理
 * 指定されたアクセスURLからデータを取得し、
 * 取得できたら所持リストの変数に追加する。
 * @param {String} accessUrl  ドレスを取得するためのアクセスURL
 * @param {String} qrcode     QRコード
 * @param {String} dressId    ドレスID
 * @param {number} len        QR登録に入力された全件数
 * @param {String} additional 付加情報
 * @param {String} aikatsuVer アイカツのバージョン stars, friends
 */
async function import_qr(accessUrl: string, qrcode: string, len: number, dressId: string | null, additional: string | null, aikatsuVer: AikatsuVersion) {
  // 取得、インポート
  try {
    const data = await (await fetch(makeRequestUrl(accessUrl))).text();

    const t = $('#qr_result').val(); // QR結果表示領域
    let resultmsg = '';
    // 正しいドレス要素のページに飛べたかチェックする
    if (!isValidDressItemPage(data)) {
      $('#qr_result').val(`${t}URL Not Found:${accessUrl}\n`);
      qr_count(len);
      return;
    }

    // ドレスアイテムのページからカード情報を取得
    let cardInfo;
    switch (aikatsuVer) {
      case 'stars':
        cardInfo = util.extractCardInfo(data);
        break;
      case 'friends':
      case 'onparade':
        cardInfo = util.extractCardInfoFriends(data);
        break;
      default:
        $('#qr_result').val(`${t}URL Not Found:${accessUrl}\n`);
        qr_count(len);
        return;
    }
    const card = cardInfo.card;
    card.qrcode = qrcode;
    if (!card.cardId) {
      $('#qr_result').val(`${t}URL Not Found:${accessUrl}\n`);
      qr_count(len);
      return;
    }

    // +対象外なのに+指定されていたら、その情報を削除する。
    if (!cardInfo.info.appealpoint_plus && additional === 'plus') {
      additional = null;
    }

    // ドレスIDが指定されてる場合は情報に加える
    if (dressId) {
      card.dressId = dressId;
      resultmsg = `${resultmsg},${dressId}`;
    }
    // カードの付加情報
    if (additional) {
      card.additional = additional;
      resultmsg = `${resultmsg},${additional}`;
    }
    // 付加情報がplusの場合はカードIDに+を付与する
    if (additional === 'plus') {
      card.cardId = `${card.cardId}+`;
    }
    // 所持リストへの格納
    window.RGList_v2 = util.grepcards('cardId', card.cardId, true);
    window.RGList_v2.push(card as Card);
    // console.log(card);
    // インポート結果メッセージ
    resultmsg = `${t + card.cardId},${card.cardName},${qrcode}`;

    // 取得結果の表示
    $('#qr_result').val(`${resultmsg}\n`);

    qr_count(len);
  } catch (error) {
    util.msgAlert('通信エラーが発生しました');
    console.log(error);
    qr_count(len);
  }
}

/**
 * QR登録中のプログレスバー
 * 呼ばれるたびにカウントを加算してプログレスバーを更新する。
 * @param {number} allTargetCount QR登録に入力されたQRコードの全件数
 */
function qr_count(allTargetCount: number) {
  $('#qr_result').animate(
    {
      scrollTop: 100000
    },
    {
      queue: false
    }
  );
  qrCounter = qrCounter + 1;
  /** 進捗状況(%) 切り上げ */
  const progressPercent = Math.ceil((qrCounter / allTargetCount) * 100);
  /** 進捗状況の％表示文字列 */
  const percentLabel = `${progressPercent}%`;

  // プログレスバーの描画更新
  $('.bar').css('width', percentLabel);
  $('.bar').text(percentLabel);

  // 全件処理完了
  if (qrCounter === allTargetCount) {
    // プログレスバーを非表示にする
    $('.indicator').fadeOut(3000);
    // ソートしてlocalStorageに格納する。
    window.RGList_v2 = util.CardNumSort(window.RGList_v2);
    localStorage.setItem('RGList_v2', JSON.stringify(window.RGList_v2));
    // 登録件数の表示を更新
    $('.RegQR').text(window.RGList_v2.length);
    // 統計情報を更新
    calcStatAll();
  }
}

/**
 * 個別のドレスページがちゃんと取れたかどうか
 * スターズとフレンズどちらかで正しく取れてたらtrue
 * @param {String} dressItemPage ドレスのHTMLのテキスト
 * @return {Boolean} true: 有効なドレスのページ, false: 無効なドレスのページ
 */
function isValidDressItemPage(dressItemPage: string) {
  if (dressItemPage === '' || dressItemPage.indexOf('みつかりませんでした') > -1 || dressItemPage.indexOf('見つかりませんでした') > -1) {
    return false;
  }
  return true;
}

/**
 * QRコードの認識開始
 */
const startRecogQR = async () => {
  stopRecogQR();
  const width = 960;
  const height = 720;

  const useDeviceId: string = $('#qrReaderDeviceSelect').val() as string;

  // デバイスの数をチェック
  let devices = await navigator.mediaDevices.enumerateDevices();
  devices = devices.filter(device => device.kind.includes('videoinput'));
  console.log(devices);

  if (devices.length > 2) {
    if (!useDeviceId) {
      alert('複数のビデオデバイスが検出されました。使用するデバイスを選択してください。');
      let htmlStr = '';
      for (let i = 0; i < devices.length; i++) {
        htmlStr += `<option value="${devices[i].deviceId}">${i}: ${devices[i].label}</option>`;
      }
      $('#qrReaderDeviceSelect').append(htmlStr);
      (document.querySelector('#qrReaderDeviceSelect') as HTMLElement).style.display = 'block';
      return;
    } else {
      (document.querySelector('#qrReaderDeviceSelect') as HTMLElement).style.display = 'block';
    }
  }

  // 要素表示
  $('#preview')
    .parent()
    .removeClass('hide');
  (document.querySelector('#qrReader') as HTMLElement).style.display = 'block';
  (document.querySelector('#qrReaderStop') as HTMLElement).style.display = 'block';

  // カメラ起動
  util.msgAlert('QRコードリーダ起動');
  const p = navigator.mediaDevices.getUserMedia({
    audio: false,
    video: {
      width,
      height,
      deviceId: useDeviceId,
      facingMode: 'environment',
      frameRate: { ideal: 60, max: 60 }
    }
  });
  p.then(mediaStream => {
    document.querySelector('video')!.srcObject = mediaStream;
    const video = document.querySelector('video') as HTMLVideoElement;
    const canv = document.createElement('canvas');
    canv.width = width;
    canv.height = height;
    const context = canv.getContext('2d') as CanvasRenderingContext2D;

    // 認識処理
    qrReaderIntervalId = window.setInterval(() => {
      context.drawImage(video, 0, 0, width, height);

      const imageData = context.getImageData(0, 0, width, height);

      const code = jsQR(imageData.data, imageData.width, imageData.height);
      if (code) {
        msgAlert(code.data);
        $('#imp_text').val(`${$('#imp_text').val()}${code.data}\n`);
        stopRecogQR();
      }
    }, 10);
  }).catch(error => {
    console.log(error);
    msgAlert('カメラ起動でエラーが発生しました');
  });
};

/**
 * QRコードの認識停止
 */
const stopRecogQR = () => {
  try {
    clearInterval(qrReaderIntervalId);
    (document.querySelector('#qrReader') as HTMLElement).style.display = 'none';
    (document.querySelector('#qrReaderStop') as HTMLElement).style.display = 'none';
    (document.querySelector('#qrReaderDeviceSelect') as HTMLElement).style.display = 'none';
    (document.querySelector('video')!.srcObject as MediaStream).getVideoTracks()[0].stop();
  } catch (e) {
    // ビデオがまだ無いときとかにここに来るが、気にしない
  }
};
