// カードのユーティリティ
import CONFIG from '../config';
import { create_privateid, makeRequestUrl, convertStrToCsv } from './common';
import { Card, Rarity, CardType, Category, CardInfo, AikatsuVersion } from '../../types/common';

/**
 * カードリストのソート処理
 * @param cardList カードリスト
 * @return カードIDでソートしたカードリスト
 */
export function CardNumSort(cardList: Card[]) {
  // ソート用のIDの生成
  const tmp: (Card & { sortId: string })[] = [];

  cardList.forEach((card: Card & { sortId: string }) => {
    const s = card.cardId.split('-');

    // +などがIDに付加されている可能性があるため、数字だけ抜き出す
    s[1] = parseInt(s[1], 10).toString();

    // Noの文字数を確認し、4桁にパディングする
    switch (s[1].length) {
      case 1:
        s[1] = '000' + s[1];
        break;
      case 2:
        s[1] = '00' + s[1];
        break;
      case 3:
        s[1] = '0' + s[1];
        break;
      default:
        // s[1] = s[1];
        break;
    }

    // 付加情報に応じて順位付け
    if (card.additional === 'plus') {
      s[1] = s[1] + '1';
    } else {
      s[1] = s[1] + '0';
    }

    // ソートIDの生成
    card.sortId = s[0].slice(-2) + '‐' + s[1] + '‐' + s[2] + '‐' + s[3];

    tmp.push(card);
  });

  // ソート処理
  tmp.sort((a, b) => {
    if (a.sortId < b.sortId) return -1;
    if (a.sortId > b.sortId) return 1;
    return 0;
  });

  // ソート用IDの削除
  const newCardList: Card[] = [];
  tmp.forEach(card => {
    delete card.sortId;
    newCardList.push(card);
  });

  return newCardList;
}

/**
 * フィルタ情報に基づいてカードリストを絞り込む
 * @param cardList カードリスト
 * @return カードリスト(絞り込み済)
 */
export function filter(cardList: Card[]) {
  // フィルタ：タイプ、レアリティを取得
  const typeList: CardType[] = [];
  const rarityList: Rarity[] = [];
  $('.dress_type')
    .find(':checked')
    .each((i, v) => {
      typeList.push((v as HTMLMetaElement).name as CardType);
    });
  $('.dress_rarity')
    .find(':checked')
    .each((i, v) => {
      rarityList.push((v as HTMLMetaElement).name as Rarity);
    });

  // フィルタ：カテゴリーを取得
  let category = '';
  $('.dress_cate')
    .find('img')
    .each((index, elem) => {
      if (
        $(elem)
          .css('filter')
          .replace(/[^0-9^.]/g, '') === '0' ||
        $(elem)
          .css('-webkit-filter')
          .replace(/[^0-9^.]/g, '') === '0'
      ) {
        category = ($(elem)
          .parent()
          .attr('class') as string).slice(-1);
      }
    });

  // タイプ、レアリティで絞り込み
  cardList = $.grep(cardList, card => {
    return $.inArray(card.type, typeList) >= 0 && $.inArray(card.rarity, rarityList) >= 0;
  });

  // カテゴリが選択されていいたら絞り込み
  if (category.length > 0) {
    cardList = $.grep(cardList, card => {
      return card.category === category;
    });
  }
  return cardList;
}

/**
 * 弾IDからアイカツのバージョンを返す
 * @param versionId 弾id
 * @return バージョン。該当しない場合はnull
 */
export function getAikatsuVersion(versionId: string): AikatsuVersion | null {
  if (parseInt(versionId, 10) < 300000) {
    return 'aikatsu';
  } else if (parseInt(versionId, 10) < 400000) {
    return 'stars';
  } else if (parseInt(versionId, 10) > 400000 && parseInt(versionId, 10) < 480000) {
    return 'friends';
  } else if (parseInt(versionId, 10) > 490000) {
    return 'onparade';
  }
  return null;
}

/**
 * カードリストから、条件に完全一致するカードを取り出す
 * @param key 検索対象のkey
 * @param value keyに対する値
 * @param invert false:合致するカードを抜き出す。true: 合致しないカードを抜き出す。
 */
export function grepcards(key: string, value: string, invert?: boolean) {
  return $.grep(
    window.RGList_v2,
    item => {
      return item[key] === value;
    },
    invert
  );
}

/**
 * 旧カツのカードを検索する
 * @param key 検索対象のkey
 * @param value 検索文字列
 * @param invert trueなら、合致しないものを返却
 */
export function grepOldCards(key: string, value: string, invert?: boolean) {
  return $.grep(
    window.OldList,
    item => {
      return item[key] === value;
    },
    invert
  );
}

/**
 * 個別のドレスページから情報抽出
 * @param dressItemPage ドレスのHTMLのテキスト
 * @return card:HTMLから取得できた一枚のカード情報。QRとかドレスIDは含まない。
 *         info:その他、ブランド名やアピールポイントなどの情報。
 */
export function extractCardInfo(dressItemPage: string) {
  // カードオブジェクトとカード情報

  const result: {
    card: Partial<Card>;
    info: Partial<CardInfo>;
  } = {
    card: {},
    info: {}
  };
  // カードID
  const infoMatch = dressItemPage.match(/<p>.*?<\/p>\w*/g);
  const cardInfo = infoMatch ? infoMatch[0].replace(/<.*?>/g, '').split(/[\s]/g) : '';
  let cardId = cardInfo[1];
  // カード名
  const nameMatch = dressItemPage.match(/<h4>.*?<\/h4>\w*/g);
  const cardName = nameMatch ? nameMatch[0].replace(/<.*?>/g, '') : '-';
  // 弾ID
  const versionMatch = dressItemPage.match(/dressitem.*?"/g);
  const versionId = versionMatch ? versionMatch[0].split('/')[1] : '-';
  // 画像URL
  const imageMatch = dressItemPage.match(/dressitem.*?"/g);
  const imageUrl = imageMatch ? imageMatch[0].split('/')[2].split('.')[0] : '-';

  const rarityList = {
    キャンペーンレア: 'cr',
    'キャンペーンレア+': 'cr',
    プレミアムレア: 'pr',
    'プレミアムレア+': 'pr',
    スタープレミアムレア: 'pr',
    'スタープレミアムレア+': 'pr',
    レア: 'r',
    'レア+': 'r',
    ノーマル: 'n',
    'ノーマル+': 'n'
  };
  const typeList = {
    キュート: 'qt',
    クール: 'co',
    セクシー: 'se',
    ポップ: 'po'
  };
  const categoryList = {
    トップス: 't',
    ボトムス: 'b',
    シューズ: 's',
    'トップス＆ボトムス': 'w',
    'トップス&ボトムス': 'w',
    アクセサリー: 'a'
  };

  if (dressItemPage.match(/<dd>.*?<\/dd>/g) !== null) {
    // 2017年度のドレスページ

    // 属性
    let tmp: any = dressItemPage.match(/<li class="type">[\s\S]*?<\/li>/);
    result.info.type = $.trim(tmp[0].match(/alt=".*?"/)[0].replace(/[alt="]/g, '')) as CardType;

    // 部位
    tmp = dressItemPage.match(/<li class="category">[\s\S]*?<\/li>/);
    result.info.category = $.trim(tmp[0].match(/.*?<\/dd>/)[0].replace(/[</dd>]/g, '')) as Category;

    // ブランド
    tmp = dressItemPage.match(/<li class="brand">[\s\S]*?<\/li>/);
    result.info.brand = $.trim(tmp[0].match(/.*?<\/dd>/)[0].replace(/[</dd>]/g, ''));

    // アピール
    tmp = dressItemPage.match(/<li class="dressappeal">[\s\S]*?<\/li>/);
    result.info.dressappeal = $.trim(tmp[0].match(/.*?<\/dd>/)[0].replace(/[</dd>]/g, ''));

    // アピールポイント
    tmp = dressItemPage.match(/<li class="appealpoint">[\s\S]*?<\/li>/);
    result.info.appealpoint = $.trim(tmp[0].match(/.*?<\/dd>/)[0].replace(/[</dd>]/g, ''));

    // アピールポイントプラス
    tmp = dressItemPage.match(/<li class="appealpoint_plus">[\s\S]*?<\/li>/);
    result.info.appealpoint_plus = $.trim(tmp[0].match(/.*?<\/dd>/)[0].replace(/[</dd>]/g, ''));

    // レアリティ
    tmp = dressItemPage.match(/<li class="reality">[\s\S]*?<\/li>/);
    result.info.rarity = $.trim(
      tmp[0]
        .match(/.*?<\/dd>/)[0]
        .replace(/[</dd>]/g, '')
        .replace(/br.*/, '')
    ) as Rarity;
  } else {
    // 2016年度のドレスページ
    let tmp: any = dressItemPage.match(/<td.*?<\/td>/g);
    result.info.type = tmp[0].match(/alt=".*?"/)[0].replace(/[alt="]/g, '');
    result.info.category = tmp[1].replace(/[</td>]/g, '') as Category;
    result.info.rarity = tmp[2].replace(/[</td>]/g, '') as Rarity;
    result.info.dressappeal = tmp[3].replace(/[</td>]/g, '');
    result.info.appealpoint = tmp[4].replace(/[</td>]/g, '');
    tmp = dressItemPage.match(/<td[\s\S]*?<\/td>/g);
    if (tmp[5].match(/alt=".*?"/)) {
      result.info.gradeUp = true;
    } else {
      result.info.gradeUp = false;
    }

    tmp = dressItemPage.match(/<div class="dress-detail-brand">[\s\S]*?<\/div>/);
    result.info.brand = tmp[0].match(/alt="(.*?)">/) ? tmp[0].match(/alt="(.*?)">/)[0] : null;
    if (result.info.brand && result.info.brand.length === 2) {
      result.info.brand = result.info.brand[1];
    } else {
      result.info.brand = 'ノーブランド';
    }
  }

  // アピールポイントの数値チェック
  if (isNaN(Number(result.info.appealpoint))) {
    result.info.appealpoint = null;
  }
  if (isNaN(Number(result.info.appealpoint_plus))) {
    result.info.appealpoint_plus = null;
  }

  // ハイフンを半角に統一する
  cardId = cardId.replace(/‐/g, '-');

  // j2プロモを-で区切る
  if (cardId.split('-').length === 3) {
    cardId = cardInfo[0] + '-' + cardId;
  }

  // カード情報
  result.card = {
    cardId,
    cardName,
    versionId,
    originalId: cardId,
    imageUrl: imageUrl.toLowerCase(),
    rarity: rarityList[result.info.rarity],
    type: typeList[result.info.type as CardType],
    category: categoryList[result.info.category]
  };
  return result;
}

/**
 * 個別のドレスページから情報抽出
 * @param dressItemPage ドレスのHTMLのテキスト
 * @return card:HTMLから取得できた一枚のカード情報。QRとかドレスIDは含まない。
 *         info:その他、ブランド名やアピールポイントなどの情報。
 */
export function extractCardInfoFriends(dressItemPage: string) {
  // カードオブジェクトとカード情報
  const result: {
    card: Partial<Card>;
    info: Partial<CardInfo>;
  } = {
    card: {},
    info: {}
  };
  const rarityList: {
    [name: string]: Rarity;
  } = {
    レジェンドプレミアムレア: 'lpr',
    ジュエリングレア: 'jlr',
    ベストフレンズレア: 'bfr',
    フレンズレア: 'fr',
    キャンペーンレア: 'cr',
    プレミアムレア: 'pr',
    レア: 'r',
    ノーマル: 'n'
  };
  const typeList: {
    [name: string]: CardType;
  } = {
    キュート: 'qt',
    クール: 'co',
    セクシー: 'se',
    ポップ: 'po'
  };
  const categoryList: {
    [name: string]: Category;
  } = {
    トップス: 't',
    ボトムス: 'b',
    シューズ: 's',
    'トップス＆ボトムス': 'w',
    'トップス&ボトムス': 'w',
    'トップス＆ボトムス＆シューズ': 'f',
    'トップス＆ボトムス＆シューズ＆アクセサリー': 'f',
    アクセサリー: 'a'
  };
  try {
    let tmp: any = dressItemPage.match(/<dd class="cardNum">([\s\S]*?)<\/dd>/);
    let cardId = tmp[1].split(/\s/)[0];
    // ハイフンを半角に統一する
    cardId = cardId.replace(/‐/g, '-');

    tmp = dressItemPage.match(/<dd class="cardName">([\s\S]*?)<\/dd>/);
    const cardName = tmp[1].split(/\s/)[0];
    /** 画像URL 複数取れる。Edgeでも開けるようにしておく。 */
    tmp = dressItemPage.match(/<img.*? src=".*?\/images\/cardlist\/card\/([\s\S]*?)"/);
    const imgUrltmp = tmp[1].split('/');
    const versionId = imgUrltmp[0];
    const imageUrl = imgUrltmp[1].split('.')[0]; // スターズの実装に合わせて拡張子を外す
    // 画像URLフルパス
    const aikatsuVer = getAikatsuVersion(versionId);
    result.info.fullImageUrl = aikatsuVer === 'friends' ? `${CONFIG.url.aifrCardImg}${versionId}/${imageUrl}.png` : `${CONFIG.url.aiopCardImg}${versionId}/${imageUrl}.png`;

    // 属性
    tmp = dressItemPage.match(/<dl class="typeCol">[\s\S]*?<\/dl>/);
    result.info.type = $.trim(tmp[0].match(/alt=".*?"/)[0].replace(/[alt="]/g, '')) as CardType;

    // 部位
    tmp = dressItemPage.match(/<dl class="attributeCol">[\s\S]*?<\/dl>/);
    result.info.category = $.trim(
      tmp[0]
        .match(/.*?<\/dd>/)[0]
        .replace(/[</dd>]/g, '')
        .replace(/br/g, '＆')
    ) as Category;

    // ブランド
    tmp = dressItemPage.match(/<dl class="brandCol">[\s\S]*?<\/dl>/);
    if (tmp[0].match(/alt=".*?"/)) {
      result.info.brand = $.trim(tmp[0].match(/alt=".*?"/)[0].replace(/[alt="]/g, ''));
    } else {
      result.info.brand = 'ノーブランド';
    }
    // スペシャルアピール
    tmp = dressItemPage.match(/<dl class="dapEffectCol">[\s\S]*?<\/dl>/);
    result.info.dressappeal = $.trim(tmp[0].match(/.*?<\/dd>/)[0].replace(/[</dd>]/g, ''));
    // アピールポイント
    tmp = dressItemPage.match(/<dl class="appealPointCol">[\s\S]*?<\/dl>/);
    result.info.appealpoint = $.trim(tmp[0].match(/.*?<\/dd>/)[0].replace(/[</dd>]/g, ''));
    // レアリティ
    tmp = dressItemPage.match(/<dl class="rarelityCol">[\s\S]*?<\/dl>/);
    result.info.rarity = $.trim(
      tmp[0]
        .match(/.*?<\/dd>/)[0]
        .replace(/[</dd>]/g, '')
        .replace(/br.*/, '')
    ) as Rarity;

    // コスメ
    tmp = dressItemPage.match(/<dl class="cosmeKindCol">[\s\S]*?<\/dl>/);
    if (tmp) {
      const cosme = tmp[0]
        .match(/.*?<\/dd>/)[0]
        .replace(/[</dd>]/g, '')
        .replace(/br.*/, '');
      result.info.cosme = $.trim(cosme);
    } else {
      result.info.cosme = null;
    }

    // カード情報
    result.card = {
      cardId,
      cardName,
      versionId,
      imageUrl: imageUrl.toLowerCase(),
      rarity: rarityList[result.info.rarity],
      type: typeList[result.info.type],
      category: categoryList[result.info.category]
    };
  } catch (e) {
    console.log('カード情報取得でエラー');
    console.log('----------stacktrace-----------');
    console.log(e);
    console.log('-----------ページ----------');
    console.log(dressItemPage);
    console.log('---------------------');
  }

  return result as {
    card: Card;
    info: CardInfo;
  };
}

/**
 * 指定された弾の旧アイカツカードを取得する
 */
export async function getOldCard(series: string): Promise<Card[]> {
  const data = await (await fetch(makeRequestUrl(CONFIG.url.akdb + 'list/' + series + '.csv?' + create_privateid(5)))).text();
  // CSVを配列に格納する
  const csvList: string[][] = await convertStrToCsv(data);

  // const csvList = $.csv(',', '', '\n')(data);
  const cardList: Card[] = [];

  csvList.forEach(csv => {
    const card: Partial<Card> = {};
    card.cardId = csv[0];
    card.cardName = csv[4];
    card.versionId = series;
    // 旧アイカツカードのレア表記体系をスターズに合わせる
    if (csv[2] === 'c' || csv[2] === 'p') {
      card.rarity = (csv[2] + 'r') as Rarity;
    } else {
      card.rarity = csv[2] as Rarity;
    }
    card.type = csv[1] as CardType;
    card.category = csv[3] as Category;
    card.qrcode = $.trim(csv[5]);
    cardList.push(card as Card);
  });
  return cardList;
}

/**
 * スターズ以降のカード画像URLを生成
 * @param versionId 弾ID
 * @param imageUrl カードオブジェクトの画像URL
 * @returns 画像URL
 */
export const makeFullImageUrl = (versionId: string, imageUrl: string): string => {
  const aikatsuVer = getAikatsuVersion(versionId);
  switch (aikatsuVer) {
    case 'stars':
      return `${CONFIG.url.aistCardImg}${versionId}/${imageUrl}.png`;
    case 'friends':
      return `${CONFIG.url.aifrCardImg}${versionId}/${imageUrl}.png`;
    case 'onparade':
      return `${CONFIG.url.aiopCardImg}${versionId}/${imageUrl}.png`;
    default:
      return '';
  }
};

/**
 * 旧カツのカード画像URLを生成
 * @param cardId カードID
 * @returns 画像URL
 */
export const makeFullImageUrlOldKatsu = (cardId: string): string => {
  return `${CONFIG.url.aiktCardImg}${cardId}.jpg`;
};
