import util from './util';
import CONFIG from './config';
import { Card, Rarity, CardType, Category } from '../types/common';
import { CardNumSort, extractCardInfo, grepcards } from './util/card';
import { makeRequestUrl } from './util/common';

/**
 * サーバのCSVから要修復のデータを削除する
 * grpsyncと一緒にやるとメンテしづらいので分ける
 */
export async function grpRepair() {
  const url = `${CONFIG.url.akdb}data/${window.GPList[0]}.csv?${util.create_privateid(5)}`;
  const data = await (await fetch(makeRequestUrl(url))).text();

  let deleteCSVList = ''; // サーバから削除する暗号化文字列
  const List = $.csv(',', '', '\n')(data); // サーバから取得した暗号化文字列のリスト
  let dectext;
  // 結果が空文字でなければ復号化する
  if (data !== '') {
    dectext = util.decrypto(window.GPList[1], List[0].toString());
  }

  if (dectext !== null) {
    // 復号化に成功した場合

    try {
      $.each(List, (i, v) => {
        dectext = util.decrypto(window.GPList[1], v.toString());

        // undefinedを含むカードは異常とみなす
        if (dectext && dectext.indexOf('undefined') === -1) {
          const cardArray = dectext.split(',');
          // IDのハイフンを半角に統一
          cardArray[0] = cardArray[0].replace(/‐/g, '-');

          // URLでカード情報が取れないプロモとかのドレスID
          let dressId = null;
          if (cardArray[4].match(/j0\?/g) || cardArray[3].match(/[A-Z]/)) {
            // QRがj0にマッチしたらdressIdを入れる
            dressId = cardArray[3];
          }

          // &parallel=_NNを削除してimageUrlにする
          cardArray[3] = cardArray[3].split('&')[0].toLowerCase();

          const rarityType = util.uniqueImageUrl(cardArray[3].toLowerCase()) as {
            type: CardType;
            rarity: Rarity;
          };
          // カード情報
          const card: Card = {
            cardId: cardArray[0], // ID,
            cardName: cardArray[1], // カード名,
            versionId: cardArray[2], // 弾ID,
            imageUrl: cardArray[3], // 画像URL,
            rarity: rarityType.rarity, // レアリティ,
            type: rarityType.type, // 属性,
            category: cardArray[3].split('_')[5].replace(/[0-9]/g, '') as Category, // 部位,
            qrcode: cardArray[4], // QR,
            dressId, // ドレスID
            additional: null
          };
          // カードの付加情報
          if (cardArray.length === 6) {
            // additionalがあったら
            card.additional = cardArray[5];
          }

          // 特定修復対象カードかチェックする
          const specChkResult = specificCardRepair(card);

          // カードの形式をチェックする
          const chkResult = checkCard(card);

          // 要修復カードはサーバから削除する
          if (specChkResult.isRepaired || chkResult.isNeedRepair) {
            deleteCSVList = deleteCSVList + v.toString() + '\n';
          }
        } else {
          // 復号化に失敗した文字列
          deleteCSVList = deleteCSVList + v.toString() + '\n';
          console.log('[復号化失敗]' + v.toString());
        }
      });
    } catch (e) {
      console.log(e);
    }
    // サーバから異常カードを削除
    if (deleteCSVList !== '') {
      $.ajax({
        url: CONFIG.url.akdb + 'data/grpdel.php',
        type: 'POST',
        data: {
          obj: deleteCSVList,
          code: window.GPList[0]
        }
      });
    }
  }
}

/**
 * 特定のカードを決め打ちで修復
 * @param card 1枚のカードオブジェクト
 * @return 固定値修復を掛けたカードオブジェクト
 */
function specificCardRepair(card: Card): { isRepaired: boolean; card: Card } {
  const result = {
    isRepaired: false,
    card
  };

  // 固定値修復
  if (card.dressId && card.dressId.match(/parallel/g) === null) {
    switch (card.cardId) {
      case 'ST-2':
        result.card.dressId = `${card.dressId}&parallel=_01`;
        result.isRepaired = true;
        break;
      case 'S-13':
        result.card.dressId = `${card.dressId}&parallel=_01`;
        result.isRepaired = true;
        break;
      case 'SZ-1':
        result.card.dressId = `${card.dressId}&parallel=_01`;
        result.isRepaired = true;
        break;
      case 'SZ-2':
        result.card.dressId = `${card.dressId}&parallel=_02`;
        result.isRepaired = true;
        break;
      case 'SMD-10':
        result.card.dressId = `${card.dressId}&parallel=_01`;
        result.isRepaired = true;
        break;
    }
    if (card.imageUrl.indexOf('np04_p04_qt_n001_wsa') > -1) {
      result.card.rarity = 'r';
    } else if (card.imageUrl.indexOf('np04_p04_se_n002_tbs') > -1) {
      result.card.rarity = 'cr';
    }
  }

  return result;
}

/**
 * 所持リスト修復
 * 所持リストの各属性をチェックし、エラーがある場合は再登録を行う。
 * 修復できないエラーの場合は、所持リストから削除する。
 */
export async function dbRepair() {
  // RGList_v2を操作するので、ループ用のリストを作成する。
  const tmpList = JSON.parse(JSON.stringify(window.RGList_v2));

  for (let card of tmpList) {
    // 特定のカードを修復
    card = specificCardRepair(card).card;

    // カードの形式をチェックする
    const chkResult = checkCard(card);

    // 属性値にエラーが見つかったとき
    if (chkResult.isNeedRepair) {
      // 対象のカードを削除する
      console.log(`形式異常：${card.cardId}`);
      window.RGList_v2 = grepcards('cardId', card.cardId, true);
    }

    // 修復が可能なら修復する
    if (chkResult.isNeedRepair && chkResult.isRecoverble) {
      // 修復用の処理

      // 検索してカード情報を取得する
      let accessUrl = null; // カード情報の取得先
      if (card.dressId) {
        accessUrl = `${CONFIG.url.aikt}stars/cardlist/dresslist_detail.php?dress_key=${card.dressId}`;
      } else {
        accessUrl = card.qrcode;
      }
      // console.log('[' + card.cardId + ']' + accessUrl);
      const data = await (await fetch(makeRequestUrl(accessUrl))).text();

      // ドレスのページが取得できたら修復処理を行う。取得できない場合は諦める。
      if (data.match(/dress-detail-title/g)) {
        // HTMLからカード情報を取得
        const fixCardInfo = extractCardInfo(data);
        const fixcard = fixCardInfo.card;
        fixcard.qrcode = card.qrcode;

        // +対象外のカードで+登録されている場合は、
        // +情報を削除する
        if (!fixCardInfo.info.appealpoint_plus && fixcard.additional === 'plus') {
          card.additional = null;
        }

        if (card.dressId) {
          fixcard.dressId = card.dressId;
        }
        if (card.additional) {
          fixcard.additional = card.additional;
        }
        // 付加情報がplusの場合はカードIDに+を付与する
        if (fixcard.additional === 'plus') {
          fixcard.cardId = `${fixcard.cardId}+`;
        }
        // 修復したカード情報を格納する
        window.RGList_v2.push(fixcard as Card);
        window.RGList_v2 = CardNumSort(window.RGList_v2);
        localStorage.setItem('RGList_v2', JSON.stringify(window.RGList_v2));
      } else {
        window.RGList_v2 = CardNumSort(window.RGList_v2);
        localStorage.setItem('RGList_v2', JSON.stringify(window.RGList_v2));
      }
    }
  }
}

/**
 * カード形式チェック
 * @param {Object} card 1枚のカードオブジェクト
 * @return {Object} isNeedRepair:修復要/不要、isRecoverble:エラー有修復可/不可
 */
function checkCard(card: Card) {
  // 修復可/不可
  // エラー有/無
  const result = {
    isNeedRepair: false,
    isRecoverble: true
  };

  // cardIdのチェック
  // IDにスペースやundefinedが入っていないこと。
  if (card.cardId.indexOf(' ') !== -1 || card.cardId.indexOf('undefined') !== -1) {
    console.log(`形式異常：[ID]${card.cardId}`);
    result.isNeedRepair = true;
  }

  // cardNameのチェック
  // 何らかの文字が入っていること。
  if (typeof card.cardName !== 'string' || card.versionId.length === 0) {
    console.log(`形式異常：[カード名]${card.cardName}`);
    result.isNeedRepair = true;
  }

  // versionIdのチェック
  // 数値文字列であること
  if (typeof card.versionId !== 'string' || isNaN(Number(card.versionId))) {
    console.log(`形式異常：[弾ID]${card.versionId}`);
    result.isNeedRepair = true;
  }

  // immageUrlのチェック
  // 何らかの文字が入っていること。
  if (typeof card.imageUrl !== 'string' || card.imageUrl.length === 0) {
    console.log(`形式異常：[画像URL]${card.imageUrl}`);
    result.isNeedRepair = true;
  }

  // rarityのチェック
  if ($.inArray(card.rarity, CONFIG.RARITYLIST) === -1) {
    console.log(`形式異常：[レアリティ]${card.rarity}`);
    result.isNeedRepair = true;
  }

  // typeのチェック
  if ($.inArray(card.type, CONFIG.TYPELIST) === -1) {
    console.log(`形式異常：[属性]${card.type}`);
    result.isNeedRepair = true;
  }

  // categoryのチェック
  if ($.inArray(card.category, CONFIG.CATEGORYLIST) === -1) {
    console.log(`形式異常：[部位]${card.category}`);
    result.isNeedRepair = true;
  }

  // qrcodeのチェック
  if (typeof card.qrcode !== 'string' || card.qrcode.length !== 84 || card.qrcode.indexOf('http://dcd.sc/') === -1) {
    console.log(`形式異常：[QR]${card.qrcode}`);
    result.isNeedRepair = true;
    result.isRecoverble = false;
  }

  // dressIdのチェック
  // しない

  // additionalのチェック
  // しない

  return result;
}
