// console.log('[プラグイン]通知画面');
import util from './util';
import { Notify } from '../types/common';
import CONFIG from './config';

/**
 * 初期化
 */
export function notifyListInit() {
  $('.m_btn-akdb').on('click', () => {
    createDOM();
  });
}

/**
 * 画面生成
 */
const createDOM = async () => {
  util.nav();
  $('.m_select').hide();
  $('.m_select option').remove();
  $('.m_card_area').hide();
  $('#Fav_display').hide();
  $('.w,.t,.b,.s,.a').empty();
  $('.favorite_list,.prom_area,.description,.delall').remove();
  $('.m_dress').empty();
  $('.m_btn_filter').hide();

  $('.l_box .m_tit span').text('お知らせ');

  let domStr = '<div class="newsContent" style="font-size:initial; color:initial">';
  const result = await createNotifyItem();
  domStr += result.domStr;
  domStr += '</div>';
  $('.m_dress').append(domStr);
};

export const createNotifyItem = async (limitId?: number): Promise<{ domStr: string; maxId: number }> => {
  let url = process.env.NODE_ENV === 'development' ? './notify.json' : `${CONFIG.url.release}notify.json`;
  url += `?id=${new Date().getTime()}`;
  const notifyListResponse: Notify = await (await fetch(url)).json();
  let maxId = 0;
  let str = '';
  for (let i = notifyListResponse.length - 1; i >= 0; i--) {
    const notify = notifyListResponse[i];
    if (limitId && limitId >= notify.id) continue;
    if (notify.id > maxId) maxId = notify.id;

    str += `<div>`;
    str += `<h4>${notify.date} ${notify.version}</h4>`;
    str += `<h5>${notify.title}</h5>`;
    str += `<ul>`;
    notify.contents.map(value => {
      str += `<li>${value}</li>`;
    });
    str += `</ul>`;
    str += '</div>';
    str += '<br/>';
  }
  return {
    domStr: str,
    maxId
  };
};
