import util from './util';
import CONFIG from './config';
import { drawCards } from './cardView';
import { Card } from '../types/common';

window.isSearchIncludeOldKatsu = false;

/**
 * 初期設定
 */
export function rgListSearchInit() {
  // メニュー追加
  $('.m_link li:nth-child(3)').before('<li class="m_btn-search-rglist"><p class="fa fa-search"></p>所持リスト検索</li>');

  $('.m_btn-search-rglist').on('click', () => {
    rgListSearchCreateDOM();
    rgListSearchEventListener();
  });
}

/**
 * 所持リスト検索の画面生成
 */
function rgListSearchCreateDOM() {
  util.nav();
  $('.m_select').hide();
  $('.m_select option').remove();
  $('.m_card_area').hide();
  $('.favorite_list,.prom_area,.description,.delall').remove();
  $('.m_dress').empty();

  $('.l_box .m_tit span').text('所持リスト検索');

  // 旧カツチェックボックスを初期化
  window.isSearchIncludeOldKatsu = false;

  let domStr = '';
  domStr +=
    '<div class="searchText"><input type="text" id="searchTextInput" class="searchTextInput" placeholder="カード名(部分一致)">' +
    '<p id="searchRGList" class="p_btn_search">検索</p>' +
    '<span class="oldKatsuSearch"><input type="checkbox" id="searchIncludeOldKatsu" class="oldKatsuSearchCheck">' +
    '<label class="oldKatsuSearchCheckLabel" for="searchIncludeOldKatsu">旧カツ</label></span>' +
    '</div>';
  domStr += '<div class="pager"></div>';
  $('.m_dress').append(domStr);
  $('.m_btn_filter').show();

  window.selectedCardList = [];
}

/**
 * 所持リスト検索のイベント定義
 */
function rgListSearchEventListener() {
  // 所持リストの検索ボタン
  $('#searchRGList').on('click', () => {
    // 検索キーワードが入力されていたら検索を実行する
    const keyword = $('#searchTextInput').val() as string;
    if (keyword) {
      let cardList = [];
      // 旧カツチェックが入ってたらそっちを検索
      if (window.isSearchIncludeOldKatsu) {
        cardList = searchOldList(keyword);
      } else {
        cardList = searchRGList(keyword);
      }

      // 表示弾のカードリストをグローバル変数に格納
      window.selectedCardList = cardList;
      // フィルター処理
      cardList = util.filter(window.selectedCardList);

      drawCards(cardList);
      util.pagenation();
    }
  });

  // 旧カツ含むチェック
  $('#searchIncludeOldKatsu').on('click', () => {
    window.isSearchIncludeOldKatsu = $('#searchIncludeOldKatsu').prop('checked');
    // 旧カツデータの取得
    if (window.isSearchIncludeOldKatsu) {
      util.loader();
      util.msgAlert('旧カツリストを読み込んでいます。');
      getNotGetOldCard()
        .then(() => {
          util.msgAlert('旧カツリストの読み込みが完了しました。');
          util.loadend();
        })
        .catch(() => {
          util.msgAlert('旧カツリストの読み込みでエラーが発生しました。');
          util.loadend();
        });
    }
  });
}

/**
 * カード名に合致するカードを所持リストから返却
 * @param keyword
 */
function searchRGList(keyword: string) {
  return $.grep(window.RGList_v2, card => {
    return card.cardName.indexOf(keyword) !== -1;
  });
}

/**
 * カード名に合致する旧カツカードを所持リストから返却
 * @param {string} keyword
 */
function searchOldList(keyword: string) {
  let cardList = $.grep(window.OldList, card => {
    return card.cardName.indexOf(keyword) !== -1;
  });
  // ソート
  cardList = util.CardNumSort(cardList);
  // 重複削除
  const arrObj: { [id: string]: Card } = {};
  for (const card of cardList) {
    arrObj[card.cardId] = card;
  }
  cardList = [];
  const keys = Object.keys(arrObj);
  for (const key of keys) {
    cardList.push(arrObj[key]);
  }

  return cardList;
}

/**
 * 未取得の旧カツカードを全て取得する
 * @todo iOS10はPromise.all対応してない
 */
async function getNotGetOldCard(): Promise<void> {
  // 未所持弾をチェックして、未所持の場合は取得タスクに加える
  CONFIG.OLDSERIES.map(async series => {
    const cards = util.grepOldCards('versionId', series.id);
    if (cards.length === 0) {
      const newList = await util.getOldCard(series.id);
      window.OldList = [...window.OldList, ...newList];
    }
  });
}
