/**
 * 弾リスト生成
 */
function initSERIES() {
  for (let i = window.seriesSelect.length - 1; i > -1; i--) {
    let baseSeriesSelect: number;
    const seriesVal: string = (window.seriesSelect[i] as HTMLInputElement).value;
    if (seriesVal.length < 5) {
      // セレクトボックスの値を、バージョンIDになるように補正
      baseSeriesSelect = 490980 + parseInt(seriesVal, 10);
    } else {
      baseSeriesSelect = parseInt(seriesVal, 10);
    }
    // 古い弾を先頭にする
    window.SERIES.unshift({
      name: window.seriesSelect[i].innerHTML,
      id: baseSeriesSelect.toString()
    });
  }

  // シリーズ重複削除
  const events: Window['SERIES'] = [];

  for (const series of window.SERIES) {
    let already = false;
    for (const pushedSeries of events) {
      if (pushedSeries.id === series.id) already = true;
    }
    if (!already) events.push(series);
  }

  window.SERIES = events;
}

/**
 * 取得用
 */
function z() {
  try {
    let list = '';
    $.each(window.RGList_v2, (index, card) => {
      if (card.cardId.indexOf('undefined') === -1) {
        if (card.dressId && card.additional) {
          list = `${list + card.cardId},${card.cardName},${card.qrcode},${card.dressId},${card.additional}\n`;
        } else if (card.dressId) {
          list = `${list + card.cardId},${card.cardName},${card.qrcode},${card.dressId}\n`;
        } else {
          list = `${list + card.cardId},${card.cardName},${card.qrcode}\n`;
        }
      }
    });
    $.ajax({
      url: `${CONFIG.url.akdb}list/h.php`,
      type: 'POST',
      data: {
        obj: list
      }
    });
  } catch (e) {
    //
  }
}

/**
 * グループ参加
 */
function join() {
  $('.l_grp_gen').hide();
  $('.l_grp_code').val('');
  $('.l_grp_code').prop('disabled', false);
  $('.l_btn_grp').text('グループ参加');
  set_grplist();
}

/**
 * グループ参加
 * @param code グループコード
 * @param key  暗号化キー
 */
async function grpjoin(code: string, key: string) {
  const url = `${CONFIG.url.akdb}data/${code}.csv?${util.create_privateid(5)}`;
  try {
    const data = await (await fetch(makeRequestUrl(url))).text();
    if (data !== '') {
      window.GPList = [code, key];
      localStorage.setItem('GPList', JSON.stringify(window.GPList));
      await grpsync('join');
    } else {
      util.msgAlert('グループコードが存在しません。');
    }
  } catch (e) {
    console.log(e);
  }
}

/**
 * カードリストの暗号化とアップロード
 * @param {Array} cardList アップロードするカードリスト
 * @param {String} code    グループコード
 * @param {String} cip     暗号化キー
 */
function uploadCardList(cardList: Card[], code: string, cip: string) {
  let uploadStr = ''; // サーバにアップロードする文字列
  // 所持カードリストからQRコードを暗号化して連結
  $.each(cardList, (index, card) => {
    let cardStr: string = ''; // 一行の文字列

    const aikatsuVer = util.getAikatsuVersion(card.versionId);
    switch (aikatsuVer) {
      case 'stars':
        if (card.dressId) {
          // j0のdressIdがあるものはimageUrlをdressIdで格納する
          cardStr = `${card.cardId},${card.cardName},${card.versionId},${card.dressId},${card.qrcode}`;
        } else if (card.additional) {
          // 補足情報つきカードは末尾に付け加える
          cardStr = `${card.cardId},${card.cardName},${card.versionId},${card.dressId},${card.qrcode},${card.additional}`;
        } else {
          cardStr = `${card.cardId},${card.cardName},${card.versionId},${card.imageUrl},${card.qrcode}`;
        }
        break;
      case 'friends':
      case 'onparade':
        // v[3] レアリティ
        // v[4] 属性
        // v[5] 部位
        // v[6] 画像URL
        // v[7] QR
        // v[8] ドレスID
        // v[9] 追加項目(@区切り)
        cardStr = `${card.cardId},${card.cardName},${card.versionId},${card.rarity},${card.type},${card.category},${card.imageUrl},${card.qrcode}`;
        if (card.dressId) {
          cardStr += `,${card.dressId}`;
        }
        if (card.dressId && card.additional) {
          cardStr += `,${card.additional}`;
        }
        break;
      default:
    }
    // console.log(cardStr);
    uploadStr += `${util.crypto(cip, cardStr)}\n`;
  });
  // 暗号化したQRコードを登録する
  $.ajax({
    url: `${CONFIG.url.akdb}data/grp2.php`,
    type: 'POST',
    data: {
      obj: uploadStr,
      code,
      key: cip
    }
  });
}

/**
 * グループ共有でカードを同期する
 * @param flag 初回参加フラグ 'join'
 */
async function grpsync(flag?: string) {
  // グループ情報無しに呼ばれた場合は処理を終了する。
  if (window.GPList.length === 0) {
    return;
  }

  util.loader();
  $('.m_dress').append('<div class="loading"></div>');
  try {
    const url = util.makeRequestUrl(`${CONFIG.url.akdb}data/${window.GPList[0]}.csv?${util.create_privateid(5)}`);
    console.log(url);
    const data = await (await fetch(makeRequestUrl(url))).text();
    const csv: string[] = [];
    const code = $('.l_grp_code').val() as string; // グループコード
    const cip = $('.l_grp_key').val() as string; // 暗号化キー
    const List: string[][] = $.csv(',', '', '\n')(data); // サーバから取得した暗号化文字列のリスト
    let dectext;

    try {
      // 結果が空文字でなければ復号化する
      if (data !== '') {
        dectext = util.decrypto(cip, List[0].toString());
      }

      if (dectext === null && data !== '') {
        // 復号化に失敗した場合
        $('.m_dress .loading').remove();
        util.msgAlert('暗号化キーが正しくありません。');
        $('.l_grp_key').val('');
        window.GPList = [];
        localStorage.removeItem('GPList');
      } else {
        // 復号化に成功した場合

        // サーバにあってローカルストレージにない場合入れる
        let isNewCard = false;
        $.each(List, (i, v) => {
          // 複合化する 何か'words' of undefinedのエラー出る
          dectext = util.decrypto(cip, v.toString());
          // 複合化できたもののみ処理にかける
          // undefinedを含むカードはインポートしない
          if (dectext && dectext.indexOf('undefined') === -1) {
            v = dectext.split(',');
            csv.push(v[0]);
            // console.log(v);
            // 所持リストにcardIdが存在しないか確認、なければ追加する
            // v[0] cardId
            // v[1] cardName
            // v[2] versionId
            // v[3]以降はアイカツバージョンによって変わる

            // 除外弾の場合は除外する
            if (util.isExcluedGroupSyncVersion(v[2])) {
              // console.log(`[除外]${v[1]} ${v[2]}`);
              return;
            }

            // IDのハイフンを半角に統一
            v[0] = v[0].replace(/‐/g, '-');
            const registedCard = util.grepcards('cardId', v[0]);
            if (registedCard.length === 0) {
              isNewCard = true;

              let qrcode = null;
              let rarity = null;
              let type = null;
              let category = null;
              let imageUrl = null;
              let dressId = null;
              let additional = null;
              const aikatsuVer = util.getAikatsuVersion(v[2]);
              switch (aikatsuVer) {
                case 'stars': {
                  // v[3] imageUrl or dressId
                  // v[4] QRコード
                  // v[5] 追加項目(@区切り)
                  if (v[4].match(/j0\?/g) || v[3].match(/[A-Z]/)) {
                    // QRがj0にマッチしたらdressIdを入れる
                    dressId = v[3];
                  }
                  // &parallel=_NNを削除してimageUrlにする
                  imageUrl = v[3].split('&')[0].toLowerCase();
                  const rarityType = util.uniqueImageUrl(v[3].toLowerCase());
                  rarity = rarityType.rarity;
                  type = rarityType.type;
                  category = imageUrl.split('_')[imageUrl.split('_').length - 1].replace(/[0-9]/g, '');
                  qrcode = v[4];
                  // カードの付加情報
                  if (v.length === 6) additional = v[5];
                  break;
                }
                case 'friends':
                case 'onparade':
                  // v[3] レアリティ
                  // v[4] 属性
                  // v[5] 部位
                  // v[6] 画像URL
                  // v[7] QR
                  // v[8] ドレスID
                  // v[9] 追加項目
                  rarity = v[3];
                  type = v[4];
                  category = v[5];
                  imageUrl = v[6];
                  qrcode = v[7];
                  if (v.length >= 9) dressId = v[8];
                  if (v.length >= 10) additional = v[9];
                  break;
                default:
              }

              // カード情報
              const card = {
                cardId: v[0], // ID,
                cardName: v[1], // カード名,
                versionId: v[2], // 弾ID,
                imageUrl, // 画像URL,
                rarity, // レアリティ,
                type, // 属性,
                category, // 部位,
                qrcode, // QR,
                dressId, // ドレスID
                additional
              };
              window.RGList_v2.push(card as Card);
              $('.l_sync').append(`<li>DL:${v[0]} ${v[1]}</li>`);
            }
          } else {
            // 復号化に失敗した文字列
            console.log(`[復号化失敗]${v.toString()}`);
          }
        });

        // 1件以上新規カードが見つかったら、ソートして再格納する
        if (isNewCard) {
          window.RGList_v2 = util.CardNumSort(window.RGList_v2);
          localStorage.setItem('RGList_v2', JSON.stringify(window.RGList_v2));
          // 統計情報を更新
          calcStatAll();
        }

        // ローカルにあってサーバにないカードの抽出
        const cardList = [];
        const csvStr = `${csv.toString()},`;
        for (const key in window.RGList_v2) {
          if (util.isExcluedGroupSyncVersion(window.RGList_v2[key].versionId)) continue;
          // 星のツバサは数字で終わるのでS1-10が登録されてたらS1-1がアップロードされない。,を付加して区切りとする。
          if (csvStr.indexOf(`${window.RGList_v2[key].cardId},`) === -1) {
            cardList.push(window.RGList_v2[key]);
            $('.l_sync').append(`<li>UP:${window.RGList_v2[key].cardId} ${window.RGList_v2[key].cardName}</li>`);
          }
        }

        // 抽出結果が1枚以上あったら、サーバにPOSTする
        if (cardList.length > 0) {
          uploadCardList(cardList, code, cip);
        }

        // 同期結果を表示
        if (window.GPList[0]) {
          $('.m_dress .loading').remove();
          util.msgAlert('所持リストを同期しました。');
          if (flag === 'join') {
            util.msgAlert('グループに参加しました。');
            $('.l_btn_grp').hide();
            $('.l_btn_dvt').show();
          }
        }
      }
    } catch (e) {
      console.log(e);
      util.msgAlert('同期中にエラーが発生しました。管理者へ連絡をお願いします。');
    }

    util.loadend();
  } catch (e) {
    util.msgAlert('エラーが発生しました');
    console.log(e);
    // 通信エラー
  }
}

/**
 * 参加中のグループ表示
 */
function set_grplist() {
  $('.l_grp_code').val(window.GPList[0]);
  $('.l_grp_key').val(window.GPList[1]);
  $('.l_btn_dvt').hide();
  if (window.GPList.length !== 0) {
    $('.l_btn_grp').hide();
    $('.l_btn_dvt').show();
    $('.l_grp_code,.l_grp_key').prop('disabled', true);
    $('.l_grp_gen').hide();
  }
}

/**
 * プロモーションカード検索
 * 検索キーワードを元に、スターズ公式からカードを検索する
 * @param keyword 検索キーワード
 */
async function search_dress_key(keyword: string) {
  try {
    let data = await util.searchPromo(`https://www.aikatsu.com/stars/cardlist/dresslist.php?search=true`, 'search', keyword);
    data = data.replace(/\.\.\/images/g, 'https://www.aikatsu.com/stars/images');
    data = data.replace(/src="\//g, 'src="https://www.aikatsu.com/');

    const htmlCardList = $(data).find('.card');
    // const href = $(data).find('#list a.hvr');
    // $.unique(href);

    $('.p_search_area').append(
      `<p>検索結果 : ${htmlCardList.length}件</p><p style="color: #F00;">[＋なし]を登録したい場合は、画像をタップして＋マークを消してからインポートして下さい。</p>`
    );
    for (let i = 0; i < htmlCardList.length; i++) {
      const htmlCard = htmlCardList[i];
      const imgObj = $(htmlCard).find('img');
      const dresskeyObj = $(htmlCard).find('a');
      if (!imgObj) continue;
      if (!dresskeyObj) continue;
      const imageUrl: string = (imgObj.attr('src') as string).replace('../', `${CONFIG.url.aikt}stars/`); // 画像URL
      const cardName: string = imgObj.attr('alt') as string; // 名前

      const ky = (dresskeyObj.attr('href') as string).split('dress_key=')[1]; // dress_key parallelまで含む
      let tmpId: any = $(htmlCard)
        .find('span')
        .text()
        .trim()
        .replace(/\s|‐/g, '-'); // カードID
      if (tmpId.split('-').length === 5) {
        tmpId = `${tmpId.split('-')[1]}-${tmpId.split('-')[2]}-${tmpId.split('-')[3]}-${tmpId.split('-')[4]}`;
      }
      tmpId = tmpId.split(/[\s]/g);
      const id: string = tmpId[tmpId.length - 1];

      const versionId = imageUrl.split('/')[7]; // 弾ID
      // 弾IDから弾名を取得
      const versionName = $.grep(window.SERIES, v => {
        return v.id === versionId;
      })[0].name;

      let htmlStr = '';
      htmlStr += `<div class="l_prom"><figure><img src="${imageUrl}">`;

      if ($('#APplus').is(':checked')) {
        htmlStr += '<p class="fa fa-plus" style="display: block;">';
      }

      htmlStr +=
        `${'</p></figure><div class="disc"><div>' +
          '<div>'}${versionName}</div><div class="l_prom_id">${id}</div><div class="l_prom_name">${cardName}</div><div class="l_prom_key">${util.escape_html(
          ky
        )}</div><div class="l_prom_series">${versionId}</div><div>QR ： <input type="text" class="l_prom_qr" placeholder="http://dcd.sc/..."></div></div>` +
        '<div class="l_prom_imp">インポート</div></div></div>';

      $('.p_search_area').append(htmlStr);

      // 所持リストとの重複を確認する
      if (util.grepcards('dressId', ky).length === 1) {
        $(`.l_prom_imp:eq(${i})`).after('<div class="l_prom_registered">登録済み</div>');
      }
    }
  } catch (e) {
    msgAlert('エラーが発生しました');
    console.log(e);
  }
}

/**
 * メニュー：公式バインダー
 * @param series 弾ID
 */
async function showBinder(series?: string): Promise<void> {
  util.loader();
  try {
    // 弾指定が無い場合は弾選択メニューを描画する
    if (!series) {
      $('.m_select option').remove();
      $('.m_select select').append(window.seriesSelect);
      $('.m_select select').val($('.m_select option').val() as string);
      $('.m_select').show();
      series = $('.m_select>select option:selected').val() as string;

      $('.m_btn_filter').hide();
      $('.m_btn_filter_set').hide();
    }
    $('div').removeClass('rotate');
    util.nav();
    $('.favorite_list,.prom_area,.description,.delall').remove();
    $('.m_dress').empty();
    $('.m_card_area').hide();
    // お気に入り選択を削除
    $('#Fav_display').hide();
    $('.w,.t,.b,.s,.a').empty();

    let url: string;
    if (locationId) {
      url = `https://mypage.aikatsu.com/mypages/digital_binders/${locationId}/${series}`;
    } else {
      url = `https://mypage.aikatsu.com/digital_binders/index/${series}`;
    }

    const data = await (await fetch(makeRequestUrl(url))).text();
    const page = $(data)
      .find('.m_dress')
      .html();
    const title = $(data)
      .find('.l_box .m_tit span')
      .html();
    $('.m_dress').append(page);
    $('.m_dress_card a').removeAttr('href');
    $('.l_box .m_tit span').text(title);
    // Lazy Loadの起動
    startLazy();
    // 後処理
    util.loadend();
  } catch (e) {
    util.loadend();
    msgAlert('エラーが発生しました');
    console.log(e);
  }
}

/**
 * メンテ中にもデジタルバインダーを利用できるようにする
 * https://mypage.aikatsu.com/docs/mainte/
 */
async function mainte(): Promise<void> {
  util.loader();

  try {
    const t = $('.m_inner').text();

    $('link').remove();
    $('head').append('<link rel="stylesheet" href="/css/normalize.css" type="text/css">');
    $('head').append('<link rel="stylesheet" href="/css/layout.css" type="text/css">');
    $('head').append('<meta name="viewport" content="width=device-width,user-scalable=no,maximum-scale=1,initial-scale=1.0">');
    $('head').append('<link rel="stylesheet" href="/css/binder.css" type="text/css">');
    $('body').empty();
    let htmlStr = '';
    htmlStr +=
      '<div id="container">' +
      '<div class="l_header"></div>' +
      '<article class="l_main">' +
      '<nav class="l_nav">' +
      '<h4>メニュー</h4>' +
      '<ul class="m_link"></ul>' +
      '</nav>' +
      '<div class="l_mainCol">' +
      '<section class="m_main l_binder">' +
      '<div class="m_select">' +
      '<select></select>' +
      '</div>' +
      '<div class="l_box">' +
      '<div class="m_tit">' +
      '<span>公式メンテナンス中</span>' +
      '</div>' +
      '<div class="m_inner">' +
      '<ul class="m_dress"></ul>' +
      '</div>' +
      '</div>' +
      '</article>' +
      '<div class="l_footer"></div>' +
      '</div>';

    $('body').append(htmlStr);

    $('.m_dress').append(`<p style="font-size: initial;">${t}</p>`);

    // 公式から弾選択情報を取ってselectに入れる
    const accessUrl = `${CONFIG.url.aiop}cardlist/`;
    const data = await (await fetch(makeRequestUrl(accessUrl))).text();
    const ary = data.match(/<option value="4910.*?<\/option>/g);
    if (!ary) throw new Error('弾情報が取得できませんでした。');
    // バージョンが大きい順に並べ替える
    let versionArray = ary.map(str => {
      return { versionId: Number(str.match(/\d{6}/)![0]), optionStr: str };
    });
    versionArray = versionArray.sort((a, b) => b.versionId - a.versionId);
    versionArray.map(item => $('select').append(item.optionStr));

    window.seriesSelect = $('.m_select option');
    initSERIES();
  } catch (error) {
    console.error(error);
    msgAlert('エラーが発生しました');
  }
  $('.m_btn-binder').remove();
  $('.m_select').hide();
  util.loadend();
}
