import util from './util';
import * as stat from './statistics';
import { Card } from '../types/common';
import { msgAlert } from './util/view';
import CONFIG from './config';

/** 検索結果として表示中のカードリスト */
let promoViewList: Card[] = [];

/** 選択状態 */
let selectedStatus = 'promo_parade';

/**
 * フレンズプロモ登録を有効にする
 */
export function friendsPromoImportInit() {
  // メニュー追加
  $('.m_link li:nth-child(6)').before('<li class="m_btn-friends-prom"><p class="fa fa-qrcode"></p>プロモ登録</li>');

  $('.m_btn-friends-prom').on('click', () => {
    friendsPromoImportCreateDOM();
    friendsPromoImportEventListener();
  });
}

/**
 * フレンズプロモ登録の画面生成
 */
function friendsPromoImportCreateDOM() {
  $('.m_select option').remove();
  util.nav();
  $('.m_card_area').hide();
  $('#Fav_display').hide();
  $('.w,.t,.b,.s,.a').empty();
  $('.m_select').hide();
  $('.m_btn_filter_set,.m_btn_filter').hide();
  $('.favorite_list,.prom_area,.description,.delall').remove();
  $('.m_dress').empty();
  $('.l_box .m_tit span').text('プロモQR登録');

  $('.m_dress').append('<div class="prom_area"></div>');
  const htmlStr =
    '<p>QR登録で404エラーになるドレスや、QRのリンク先が実際のカードと異なる場合は、手動でインポートする必要があります。</br>' +
    '（QR URLがn0のドレスなどが該当します）</br>' +
    '</br>' +
    '【登録手順】</br>' +
    '1.キーワードにドレス名 or カードIDを入力して検索</br>' +
    '2.該当ドレスのQR欄にURLを入力してインポート</br>' +
    '<small>※誤って登録した場合はQRを上書きインポートできます。</small></br>' +
    '<small>※登録済QRはQR登録のエクスポート結果の形式でインポートできます。</small></br></br></p>' +
    '<div>' +
    '<div class="l_grp">' +
    '<input type="radio" name="promo_switch" id="promo_parade" checked value="promo_parade"><label for="promo_parade" class="switch">オンパレード</label>' +
    '<input type="radio" name="promo_switch" id="promo_friends" value="promo_friends"><label for="promo_friends" class="switch">フレンズ</label>' +
    '<input type="radio" name="promo_switch" id="promo_stars" value="promo_stars"><label for="promo_stars" class="switch">スターズ</label>' +
    '</div>' +
    '<div>キーワード：<input type="text" class="search_prom"> <p class="p_btn_search">検索</p></div>' +
    '</div>';
  $('.prom_area').append(htmlStr);
  $('.prom_area').append('<div class="p_search_area"></div>');
}

/**
 * フレンズプロモ登録のイベント定義
 */
function friendsPromoImportEventListener() {
  // プロモ登録の種類の切替ボタン
  $('[name="promo_switch"]').on('click', (event: JQuery.ClickEvent<HTMLInputElement>) => {
    // 状態を更新
    selectedStatus = event.target.value;
    // 項目をクリア
    $('.p_search_area').empty();
  });

  // プロモ登録の検索ボタン
  $('.p_btn_search').on('click', () => {
    const searchWord = $('.search_prom').val() as string;
    console.log(`種別：${selectedStatus} 検索文字：${searchWord}`);

    if (!searchWord) {
      msgAlert('検索文字を入力してください。');
      return;
    }

    $('.p_search_area').empty();
    msgAlert('検索しています');

    switch (selectedStatus) {
      case 'promo_parade':
        searchOfficialFriendsDress(searchWord, 'onparade').then(cardList => {
          showPromoSearchResult(cardList);
        });
        break;
      case 'promo_friends':
        searchOfficialFriendsDress(searchWord, 'friends').then(cardList => {
          showPromoSearchResult(cardList);
        });
        break;
      case 'promo_stars':
        searchStarsCard(searchWord);
        break;
    }
  });
}

/**
 * インポートボタン押した時の各種入力パラメータを取得する
 * @param importButton
 */
const extractInputParam = (importButton: HTMLElement) => {
  // 入力されたQRコード
  const qrcode = $(importButton)
    .parent()
    .find('.l_prom_qr')
    .val() as string;
  // 比較用のcardId
  const cardId = $(importButton)
    .parent()
    .find('.l_prom_id')
    .text() as string;

  const card = promoViewList.filter(item => item.cardId === cardId)[0];
  card.qrcode = qrcode;

  return card;
};

/**
 * フレンズ、オンパレードプロモの登録
 * @param qrcode
 * @param cardId
 */
function friendsPromoImport(card: Card) {
  let result = false;
  try {
    // カードを登録する
    window.RGList_v2 = util.grepcards('cardId', card.cardId, true);
    window.RGList_v2.push(card);
    window.RGList_v2 = util.CardNumSort(window.RGList_v2); // ソート
    localStorage.setItem('RGList_v2', JSON.stringify(window.RGList_v2));

    // 統計情報を更新
    stat.calcStatSpecific([card.versionId]);
    result = true;
  } catch (e) {
    msgAlert('インポートに失敗しました。');
  }
  return result;
}

// QRインポートボタンを表示
function showImportButton(textBox: HTMLElement) {
  $(textBox).css({
    color: '#00F'
  });
  $(textBox)
    .closest('.disc')
    .find('.l_prom_imp')
    .show();
}
// QRインポートボタンを非表示
function hideImportButton(textBox: HTMLElement) {
  $(textBox).css({
    color: '#F00'
  });
  $(textBox)
    .closest('.disc')
    .find('.l_prom_imp')
    .hide();
}

/**
 * QRの型チェック
 * @param  qrcode QRコードの文字列
 * @return 有効ならtrue
 */
function checkQRCode(qrcode: string): boolean {
  // 文字長チェック
  if (qrcode.length !== 84) return false;
  // ドメインチェック
  if (qrcode.indexOf('dcd.sc/') === -1) return false;

  return true;
}

/**
 * フレンズ、オンパレードカード検索
 * 検索キーワードを元に、公式からカードを検索する
 * @param keyword 検索キーワード
 * @return カードリストのオブジェクト配列
 */
function searchOfficialFriendsDress(keyword: string, aikatsuType: 'friends' | 'onparade'): Promise<ReturnType<typeof util.extractCardInfoFriends>[]> {
  return new Promise(async (resolve, reject) => {
    let url: string = '';
    switch (aikatsuType) {
      case 'friends':
        url = 'https://aikatsu.com/friends/cardlist/index.php?search=true';
        break;
      case 'onparade':
        url = 'https://aikatsu.com/onparade/cardlist/index.php?search=true';
        break;
      default:
        url = 'https://aikatsu.com/onparade/cardlist/index.php?search=true';
    }

    let data = await util.searchPromo(url, 'search', keyword);

    data = data.replace(/\.\.\/images/g, `http://www.aikatsu.com/${aikatsuType}/images`);
    data = data.replace(/src="\//g, 'src="http://www.aikatsu.com/');

    const cardInfoList: ReturnType<typeof util.extractCardInfoFriends>[] = [];
    promoViewList = [];
    const dressIdList = extractCardDressIdFriends(data);
    $(data)
      .find('#lists > .data')
      .each((index, cardDetail) => {
        // カード情報＋ドレスID
        const cardInfo = util.extractCardInfoFriends(cardDetail.outerHTML);
        const target = $.grep(dressIdList, v => {
          return v.cardId === cardInfo.card.cardId;
        });
        cardInfo.card.dressId = target[0].dressId;
        promoViewList.push(cardInfo.card);
        cardInfoList.push(cardInfo);
      });
    resolve(cardInfoList);
  });
}

/**
 * 検索結果表示
 * @param cardInfoList 表示対象のカードリスト
 */
function showPromoSearchResult(cardInfoList: ReturnType<typeof util.extractCardInfoFriends>[]) {
  $('.p_search_area').append('<p>検索結果 : ' + cardInfoList.length + '件</p>');

  let i = 0;
  for (const cardInfo of cardInfoList) {
    // 弾IDから弾名を取得
    const seriesName = $.grep(window.SERIES, v => {
      return v.id === cardInfo.card.versionId;
    })[0].name;
    const htmlStr = `
<div class="l_prom">
  <figure><img src="${cardInfo.info.fullImageUrl}"></p></figure>
  <div class="disc">
  <div>
    <div>${seriesName}</div>
      <div class="l_prom_id">${cardInfo.card.cardId}</div>
      <div class="l_prom_name">${cardInfo.card.cardName}</div>
      <div class="l_prom_series">${cardInfo.card.versionId}</div>
      <div>QR ： <input type="text" class="l_prom_qr" placeholder="http://dcd.sc/..."></div>
    </div>
    <div class="l_prom_imp">インポート</div>
  </div>
</div>`;
    $('.p_search_area').append(htmlStr);

    // 所持リストとの重複を確認する
    if ((cardInfo.card.dressId && util.grepcards('dressId', cardInfo.card.dressId).length === 1) || util.grepcards('cardId', cardInfo.card.cardId).length !== 0) {
      $(`.l_prom_imp:eq(${i})`).after('<div class="l_prom_registered">登録済み</div>');
    }
    i++;
  }

  // インポートボタン
  $('.l_prom_imp').on('click', function() {
    const card = extractInputParam(this);
    const result = friendsPromoImport(card);
    if (result) {
      // インポートボタンを非表示にする
      $(this).hide();
      util.msgAlert(card.cardId + 'をインポートしました');
      $(this)
        .next()
        .remove();
      $(this).after('<div class="l_prom_registered">登録済み</div>');
    }
  });

  // 入力チェック
  $('.l_prom_qr').on('focusout', function() {
    const qrcode = $(this).val() as string;
    if (checkQRCode(qrcode)) {
      showImportButton(this);
    } else {
      hideImportButton(this);
    }
  });
}

/**
 * 検索結果からドレスIDとカードIDの対応を取得
 * @param dressItemPage カードリストのHTMLのテキスト
 * @return カードIDとドレスIDのペアのオブジェクト配列
 */
function extractCardDressIdFriends(dressItemPage: string) {
  const dressIdList: {
    cardId: string;
    dressId: string;
  }[] = [];
  try {
    $(dressItemPage)
      .find('#image_lists .imageListCol')
      .each((i, v) => {
        const cardId = $(v)
          .find('dt')[0]
          .innerText.split(' ')[0];
        const dressId = $(v)
          .find('a')!
          .attr('href')!
          .split(/\?|&/)[1]
          .split('=')[1];
        dressIdList.push({ cardId, dressId });
      });
  } catch (e) {
    console.log('カード情報取得でエラー');
    console.log('----------stacktrace-----------');
    console.log(e);
    console.log('-----------ページ----------');
    console.log(dressItemPage);
    console.log('---------------------');
  }

  return dressIdList;
}

/**
 * スターズプロモーションカード検索
 * 検索キーワードを元に、スターズ公式からカードを検索する
 * @param keyword 検索キーワード
 */
async function searchStarsCard(keyword: string) {
  try {
    let data = await util.searchPromo(`https://www.aikatsu.com/stars/cardlist/dresslist.php?search=true`, 'search', keyword);
    data = data.replace(/\.\.\/images/g, 'https://www.aikatsu.com/stars/images');
    data = data.replace(/src="\//g, 'src="https://www.aikatsu.com/');

    const htmlCardList = $(data).find('.card');

    $('.p_search_area').append(
      `<p>検索結果 : ${htmlCardList.length}件</p><p style="color: #F00;">[＋あり]を登録したい場合は、画像をタップして＋マークを追加してからインポートして下さい。</p>`
    );
    promoViewList = [];

    for (let i = 0; i < htmlCardList.length; i++) {
      const htmlCard = htmlCardList[i];
      const imgObj = $(htmlCard).find('img');
      const dresskeyObj = $(htmlCard).find('a');
      if (!imgObj) continue;
      if (!dresskeyObj) continue;
      const imageUrl: string = (imgObj.attr('src') as string).replace('../', `${CONFIG.url.aikt}stars/`); // 画像URL
      const cardName: string = imgObj.attr('alt') as string; // 名前

      const dressId = (dresskeyObj.attr('href') as string).split('dress_key=')[1]; // dress_key parallelまで含む
      let tmpId: any = $(htmlCard)
        .find('span')
        .text()
        .trim()
        .replace(/\s|‐/g, '-'); // カードID
      if (tmpId.split('-').length === 5) {
        tmpId = `${tmpId.split('-')[1]}-${tmpId.split('-')[2]}-${tmpId.split('-')[3]}-${tmpId.split('-')[4]}`;
      }
      tmpId = tmpId.split(/[\s]/g);
      const cardId: string = tmpId[tmpId.length - 1];

      const versionId = imageUrl.split('/')[7]; // 弾ID
      // 弾IDから弾名を取得
      const versionName = $.grep(window.SERIES, v => {
        return v.id === versionId;
      })[0].name;

      const card: Card = {
        cardId,
        cardName,
        versionId,
        imageUrl,
        dressId,
        rarity: 'n',
        type: 'qt',
        category: 't',
        qrcode: '',
        additional: ''
      };

      promoViewList.push(card);

      const htmlStr = `
<div class="l_prom">
  <figure><img src="${imageUrl}"><p class="fa fa-plus" style="display: none;"></p></figure>
  <div class="disc">
    <div>
      <div>${versionName}</div>
      <div class="l_prom_id">${cardId}</div>
      <div class="l_prom_name">${cardName}</div>
      <div class="l_prom_key">${util.escape_html(dressId)}</div>
      <div class="l_prom_series">${versionId}</div>
      <div>QR ： <input type="text" class="l_prom_qr" placeholder="http://dcd.sc/..."></div>
    </div>
    <div class="l_prom_imp">インポート</div>
  </div>
</div>`;

      $('.p_search_area').append(htmlStr);
      // 所持リストとの重複を確認する
      if ((card.dressId && util.grepcards('dressId', card.dressId).length === 1) || util.grepcards('cardId', card.cardId).length !== 0) {
        $(`.l_prom_imp:eq(${i})`).after('<div class="l_prom_registered">登録済み</div>');
      }
    }

    // AP+の切替
    $('figure img').on('click', event => {
      $(event.target)
        .next()
        .toggle();
    });

    // インポートボタン
    $('.l_prom_imp').on('click', function() {
      const targetCard = extractInputParam(this);
      const isPlus = $(this)
        .parent()
        .parent()
        .find('figure p')
        .is(':visible');
      const result = starsPromoImport(targetCard, isPlus);
      if (result) {
        // インポートボタンを非表示にする
        $(this).hide();
        util.msgAlert(targetCard.cardId + 'をインポートしました');
        $(this)
          .next()
          .remove();
        $(this).after('<div class="l_prom_registered">登録済み</div>');
      }
    });

    // 入力チェック
    $('.l_prom_qr').on('focusout', function() {
      const qrcode = $(this).val() as string;
      if (checkQRCode(qrcode)) {
        showImportButton(this);
      } else {
        hideImportButton(this);
      }
    });
  } catch (e) {
    msgAlert('エラーが発生しました');
    console.log(e);
  }
}

/**
 * スターズプロモのインポート
 * @param baseCard
 * @param isPlus
 */
const starsPromoImport = async (baseCard: Card, isPlus: boolean) => {
  let result = false;
  try {
    const url = `${CONFIG.url.aist}cardlist/dresslist_detail.php?dress_key=${baseCard.dressId}`;
    const data = await (await fetch(util.makeRequestUrl(url))).text();

    // 元のデータはダミーデータなので、ちゃんとしたデータを取得する
    const cardInfo = util.extractCardInfo(data);
    const card = cardInfo.card as Card;
    card.qrcode = baseCard.qrcode;
    card.dressId = baseCard.dressId;

    // APplusがチェックされている場合はカードIDに記号+を追加する
    // +対象外なのに+指定されたら登録を中断する。
    if (isPlus && !cardInfo.info.appealpoint_plus && card.additional === 'plus') {
      util.msgAlert(`${card.cardId}はアピールポイント＋になるカードではありません。カードID、+有無を再度ご確認下さい。`);
      return;
    } else if (isPlus) {
      card.cardId = `${card.cardId}+`;
    }

    // カードを登録する
    window.RGList_v2 = util.grepcards('cardId', card.cardId, true);
    window.RGList_v2.push(card as Card);
    window.RGList_v2 = util.CardNumSort(window.RGList_v2); // ソート
    localStorage.setItem('RGList_v2', JSON.stringify(window.RGList_v2)); // 登録

    // 統計情報を更新
    stat.calcStatAll();
    result = true;
  } catch (e) {
    msgAlert('エラーが発生しました');
    console.log(e);
  }
  return result;
};
