// console.log('[プラグイン]設定画面');
import util from './util';

/**
 * 初期化
 */
export function settingInit() {
  // メニュー追加
  $('.m_btn-akdb').before('<li class="m_btn-setting"><p class="fa fa-cog"></p>設定</li>');

  $('.m_btn-setting').on('click', () => {
    settingCreateDOM();
    settingEventListener();
  });
}

/**
 * 画面生成
 */
function settingCreateDOM() {
  util.nav();
  $('.m_select').hide();
  $('.m_select option').remove();
  $('.m_card_area').hide();
  $('#Fav_display').hide();
  $('.w,.t,.b,.s,.a').empty();
  $('.favorite_list,.prom_area,.description,.delall').remove();
  $('.m_dress').empty();
  $('.m_btn_filter').hide();

  $('.l_box .m_tit span').text('設定');

  let domStr = '<div style="font-size:initial">';
  // グループ共有除外設定
  domStr += '<div class="setting-container">';
  domStr += '<div><h3>グループ共有除外弾設定</h3></div>';
  domStr += 'グループ共有のUP/DOWNから除外する弾を指定します。';
  domStr += '<ul style="margin-left:20px">';
  for (const series of window.SERIES) {
    const checked = util.isExcluedGroupSyncVersion(series.id) ? 'checked' : '';
    domStr += `<li style="list-style: none"><label><input type="checkbox" class="groupExclusionCheck" name="excludeGroupSync" value="${series.id}" ${checked}>${
      series.name
    }</label></li>`;
  }
  domStr += '</ul>';
  domStr += '<input type="button" id="groupShareIgnoreVersionButton" value="除外設定">';
  domStr += '</div>';

  // その他QRエクスポート
  domStr += '<div class="setting-container">';
  domStr += '<div><h3>その他QRエクスポート</h3></div>';
  domStr += '<div>その他QRに登録している情報をエクスポートします。</div>';
  domStr += '<div><input type="button" id="otherQRExportButton" value="エクスポート"></div>';
  domStr += '<div><textarea style="width:100%" id="otherQRExportArea"></textarea></div>';
  domStr += '</div>';

  // お気に入りエクスポート
  domStr += '<div class="setting-container">';
  domStr += '<div><h3>お気に入りエクスポート</h3></div>';
  domStr += '<div>お気に入りに登録している情報をエクスポートします。</div>';
  domStr += '<div><input type="button" id="fvListExportButton" value="エクスポート"></div>';
  domStr += '<div><textarea style="width:100%" id="fvListExportArea"></textarea></div>';
  domStr += '</div>';

  // QR登録数
  domStr += '<div class="setting-container">';
  domStr += '<div><h3>登録済みQR総数</h3></div>';
  domStr += `<div>登録数：${window.RGList_v2.length}</div>`;
  domStr += '</div>';

  // お気に入り登録数
  const favNum = Object.keys(window.FVList_v2).length;
  domStr += '<div class="setting-container">';
  domStr += '<div><h3>お気に入り総数</h3></div>';
  domStr += `<div>登録数：${favNum}</div>`;
  domStr += '</div>';

  // localStorageデータ容量
  const localStorageUsage = getLocalStorageUsageMBytes();
  domStr += '<div class="setting-container">';
  domStr += '<div><h3>使用中データ量表示</h3></div>';
  domStr += `<div>使用量：${localStorageUsage}MByte</div>`;
  domStr += '<small>ブラウザごとに利用できる容量が異なります。5MByteが上限のブラウザが多いです。</small>';
  domStr += '</div>';

  domStr += '</div>';
  $('.m_dress').append(domStr);
}

/**
 * イベント定義
 */
function settingEventListener() {
  $('#groupShareIgnoreVersionButton').on('click', () => {
    try {
      setGroupShareIgnoreVersion();
      util.msgAlert('除外弾を設定しました');
    } catch (e) {
      util.msgAlert('エラーが発生しました');
    }
  });
  // エクスポート
  $('#otherQRExportButton').on('click', () => {
    $('#otherQRExportArea').val(exportIDList());
  });
  $('#fvListExportButton').on('click', () => {
    $('#fvListExportArea').val(exportFVList());
  });
}

function setGroupShareIgnoreVersion() {
  const versions: string[] = [];
  $('input[name="excludeGroupSync"]:checked').each(function() {
    versions.push($(this).val() as string);
  });
  localStorage.setItem('GrpSyncIgnoreVersion', versions.toString());
}

/**
 * その他QRのエクスポート
 * @return エクスポート結果文字列
 */
function exportIDList() {
  let listStr = '';
  if (localStorage.IDList) {
    const keys = Object.keys(window.IDList);
    for (const key of keys) {
      listStr += `${key},${window.IDList[key]}\n`;
    }
  }
  return listStr;
}

/**
 * お気に入りのエクスポート
 * @return エクスポート結果文字列
 */
function exportFVList() {
  let listStr = '';
  if (localStorage.FVList_v2) {
    const keys = Object.keys(window.FVList_v2);
    for (const key of keys) {
      const list = window.FVList_v2[key].toString().replace(/\n/g, '');
      listStr += `${key},${list}\n`;
    }
  }
  return listStr;
}

/**
 * localStorageで使っている容量を取得する
 * @return {Number} 使用量
 */
function getLocalStorageUsageMBytes() {
  let localStorageStr = '';
  Object.keys(localStorage).forEach(key => {
    localStorageStr += localStorage[key];
  });
  // 文字列の容量をMByteに変換する
  const size = (bytes2(localStorageStr) / 1024 / 1024).toFixed(2);
  return size;
}

// 文字列のバイト数を数える
function bytes2(str: string) {
  return encodeURIComponent(str).replace(/%../g, 'x').length;
}
