import CONFIG from './config';
import util from './util';
import { Card } from '../types/common';
import { msgAlert } from './util/view';
import { makeFullImageUrlOldKatsu, makeFullImageUrl } from './util/card';

/**
 * カードリストのDOM生成
 * @param cardList 描画するカードリスト
 */
export function drawCards(cardList: Card[]) {
  try {
    // カードリストを初期化
    $('.m_dress > .m_dress_card').remove();

    // カードリストのDOMを生成
    let cardListDOM = '';
    cardList.forEach(card => {
      cardListDOM += '<li class="m_dress_card">';

      // 属性
      cardListDOM += '<a class="type_' + card.type.toLowerCase() + '">';

      // カード画像URL
      let imgUrl;
      switch (util.getAikatsuVersion(card.versionId)) {
        case 'aikatsu':
          imgUrl = makeFullImageUrlOldKatsu(card.cardId);
          break;
        case 'stars':
        case 'friends':
        case 'onparade':
          imgUrl = makeFullImageUrl(card.versionId, card.imageUrl);
          break;
      }
      // 読み込み待ち中の画像URL
      const altUrl = CONFIG.CATEGORY_ICON_URL[card.category.toLowerCase()];

      // カード画像
      cardListDOM +=
        '<div class="m_dress_card_img">' +
        '<img class="lazy"' +
        'src="' +
        altUrl +
        '"" data-original="' +
        imgUrl +
        '" title="' +
        card.cardName +
        '" data-clist="' +
        card.cardId +
        '@' +
        card.type +
        '@' +
        card.rarity +
        '@' +
        card.category +
        '@' +
        card.qrcode +
        '">' +
        '</div>';

      // レアリティとカードID
      cardListDOM += '<div class="m_dress_card_name rarity_' + card.rarity + '">' + card.cardId + '</div>';
      cardListDOM += '</a>';
      cardListDOM += '</li>';
    });

    $('.m_dress').append(cardListDOM);
    util.startLazy();
  } catch (e) {
    msgAlert('エラーが発生しました');
    console.log(e);
  }
}
