/**
 * お気に入り登録、表示
 */
import util from './util';
import CONFIG from './config';
import { startLazy } from './util/view';
import { showRegCard, showOldCard } from './showList';
import { makeFullImageUrl } from './util/card';

type FavList = {
  favId: string;
  cards: { cardId: string; qrcode: string; category: string; imageUrl: string; altUrl: string; altName: string; dataClist: string }[];
}[];

/**
 * スクリプト起動時の統計処理
 */
export function favListInit() {
  // メニュー お気に入りリスト
  $('.m_btn-fav').on('click', () => {
    showFavList();
  });

  // お気に入りトップに戻る
  $(document).on('click', '.back_to_favlist_top', () => {
    showFavList();
  });

  // お気に入りボタン
  $(document).on('click', '#SetFav', () => {
    saveCardToFavList();
  });

  // お気に入りリスト 画像タップ
  $(document).on('click', '.favorite_list img', function() {
    const cardObjects = $(this)
      .closest('.favorite_list')
      .find('.m_dress_card_name');
    showFavQR(cardObjects as any);
  });

  // お気に入り削除処理
  $(document).on('click', '.fav_del', function() {
    const favId = $(this).attr('title') as string;
    deleteFavItem(favId);
  });

  // お気に入り編集処理
  $(document).on('click', '.fav_edit', function() {
    const cardList = $(this)
      .parent()
      .parent()
      .find('.m_dress_card');
    editFavItem(cardList as any);
  });

  // お気に入りグループ表示処理
  $(document).on('click', '.fav_group_show', function() {
    const favGroupId = $(this).attr('title') as string;
    showFavGroupItem(favGroupId);
    // const fav_group_id = $(this).attr('title');
    // showFavGroup(fav_group_id);
  });

  // お気に入りグループ削除処理
  $(document).on('click', '.fav_group_del', function() {
    const favGroupId = $(this).attr('title') as string;
    deleteFavGroupItem(favGroupId);
  });

  // お気に入りグループ編集処理
  $(document).on('click', '.fav_group_edit', function() {
    const favGroupId = $(this).attr('title') as string;
    renameFavGroup(favGroupId);
  });

  // お気に入りグループ追加
  $(document).on('click', '#fav_group_add', () => {
    addFavGroupItem();
  });

  // グループ登録済みお気に入り表示
  $(document).on('click', '#fav_group_edit_registered', function() {
    const favGroupId = $(this).attr('data-favId') as string;
    showGroupRegisteredFavList(favGroupId, true);
  });

  // グループ未登録お気に入り表示
  $(document).on('click', '#fav_group_edit_not_registered', function() {
    const favGroupId = $(this).attr('data-favId') as string;
    showGroupRegisteredFavList(favGroupId, false);
  });

  // グループにお気に入りアイテムを追加
  $(document).on('click', '.fav_group_item_add', function() {
    const favGroupId = $(this).attr('data-favGroupId') as string;
    const favId = $(this).attr('data-favId') as string;
    addEditFavGroupItem(favGroupId, favId);
  });

  // グループからお気に入りアイテムを削除
  $(document).on('click', '.fav_group_item_del', function() {
    const favGroupId = $(this).attr('data-favGroupId') as string;
    const favId = $(this).attr('data-favId') as string;
    removeEditFavGroupItem(favGroupId, favId);
  });
}

/**
 * お気に入りグループ名の変更
 * @param {string} favGroupId お気に入りグループID
 */
function renameFavGroup(favGroupId: string) {
  const targetGroup = getFavGroup(favGroupId)[0];
  const name = window.prompt('変更するお気に入りグループ名を入力してください', targetGroup.name);
  if (!name || name === '') return;

  // 入力したグループ名に変更
  for (const group of window.FavGroup) {
    if (group.id === favGroupId) {
      group.name = name;
      break;
    }
  }
  // 保存
  localStorage.setItem('FavGroup', JSON.stringify(window.FavGroup));

  // 画面要素の値を上書き
  $(`.fav_group_show[title="${favGroupId}"]`).text(name);
}

/**
 * お気に入りボタン
 * 選択されたカードをお気に入りに保存する
 */
function saveCardToFavList() {
  /** 新規登録するお気に入りカードのIDの配列 */
  const favCards: string[] = [];
  /** お気に入りのセットを表すユニークな文字列 */
  let favId = '';

  // お気に入りのユニークなIDとお気に入りの配列を生成
  $('#Fav_display')
    .find('img')
    .each((index, elem) => {
      // 旧アイカツ
      // v[0] cardId
      // v[1] type
      // v[2] rarity
      // v[3] category
      // v[4] QR
      const clist = $(elem)
        .attr('data-clist')!
        .split('@');

      favCards.push(clist[0]);
      // カードIDを連結してユニークなお気に入りIDを生成
      favId = favId + '_' + clist[0];
      if (clist[4].indexOf('dcd.sc') === -1) {
        // 旧カードはQRも入れる
        // v1： ID_QR
        // v2： ID_QR@属性@レア@部位
        favCards[index] = `${clist[0]}_${clist[4].replace(/\r/g, '')}@${clist[1]}@${clist[2]}@${clist[3]}`;
      }
    });

  // お気に入りを保存する
  window.FVList_v2[favId] = favCards;
  localStorage.setItem('FVList_v2', JSON.stringify(window.FVList_v2));
  util.msgAlert('お気に入りへ保存しました');
}

/**
 * お気に入りリストを表示する
 */
function showFavList() {
  $('.m_select option').remove();
  util.nav();
  $('.m_card_area').hide();
  $('#Fav_display').hide();
  $('.w,.t,.b,.s,.a').empty();
  $('.m_select').hide();
  $('.m_btn_filter_set,.m_btn_filter').hide();
  $('.favorite_list,.prom_area,.description,.delall').remove();
  $('.m_dress').empty();
  $('.l_box .m_tit span').text('お気に入りリスト');

  // お気に入りリストに格納されているカードを描画する
  // お気に入りフォルダのリストを描画
  const favGroupDomStr = generateFavGroupListDom(window.FavGroup);
  $('.m_dress').append(favGroupDomStr);

  $('.m_dress').append('<hr>');
  $('.m_dress').append('<div class="header_label">全てのお気に入り一覧</div>');

  /** お気に入り描画のDOM */
  const favobject = convertFavListToShowList(Object.keys(window.FVList_v2));
  const favListDomStr = generateFavListDom(favobject);
  $('.m_dress').append(favListDomStr);

  // Lazy Loadの起動
  startLazy();
}

/**
 * お気に入りドレスタップ時のQR表示
 * @param {HTMLDataListElement} cardObjects タップしたお気に入りコーデが持っている画像DOMたち
 */
function showFavQR(cardObjects: HTMLElement[]) {
  // QR表示域の初期化
  $('.dress_w,.dress_t,.dress_b,.dress_s,.dress_a').empty();
  $('#QR_display canvas').remove();

  const cardList = [];
  // タップしたお気に入りセットをカードリストに整形
  for (const cardObject of cardObjects) {
    const card = {
      cardId: $(cardObject).attr('data-cardId') as string,
      category: $(cardObject).attr('data-category') as string,
      qrcode: $(cardObject).attr('data-qr') as string
    };
    cardList.push(card);
  }

  // ドレスごとのQRを表示域に表示
  for (const card of cardList) {
    const options = util.qrcode_option(card.qrcode, card.cardId);
    $(`.dress_${card.category}`).qrcode(options);
    $(`.dress_${card.category}`).append(card.cardId);
  }

  // QRコードごとの表示幅
  $('#QR_display canvas').css({
    width: '150px'
  });
  // QR画面を表示する
  $('#QR_display').fadeIn();
}

/**
 * お気に入りアイテムの削除
 * @param {string}} favId お気に入りID
 */
function deleteFavItem(favId: string) {
  if (confirm('お気に入りを削除しますか？')) {
    delete window.FVList_v2[favId];
    localStorage.setItem('FVList_v2', JSON.stringify(window.FVList_v2));
    $(`[data-favid="${favId}"]`).remove();
  }
}

/**
 * お気に入り編集処理
 * @param {HTMLDataListElement} dressCardList
 */
function editFavItem(dressCardList: HTMLElement[]) {
  if (!confirm('お気に入りを編集しますか？')) return;
  // QR表示領域の初期化
  $('.t,.b,.s,.a,.w').empty();

  // カード選択欄にカードオブジェクトをコピーする
  $.each(dressCardList, (i, e) => {
    const category = $(e)
      .find('img')!
      .attr('data-clist')!
      .split('@')[3];
    if (category === 't' || category === 'b' || category === 's' || category === 'a' || category === 'w') {
      $(`.${category}`).append(e);
    }
  });

  /** お気に入りセットの最初の1枚のカードID */
  let firstFavCardId: string = '';
  /** お気に入りセットの最初の1枚のカード画像 */
  let firstFavCardImg: string = '';

  // カード選択欄にお気に入りのカードを表示する
  $('#Fav_display')
    .find('.m_dress_card_img')
    .each((index, elem) => {
      const qrcode = $(elem)
        .find('img')!
        .attr('data-clist')!
        .split('@')[4];
      const cardId = $(elem)
        .find('img')!
        .attr('data-clist')!
        .split('@')[0];

      if (firstFavCardId === '') {
        firstFavCardId = cardId;
        firstFavCardImg = $(elem)
          .find('img')
          .attr('src') as string;
      }

      // 空っぽのcanvasオブジェクトが残ってるので削除する
      $(elem)
        .parent()
        .parent()
        .find('canvas')
        .remove();

      // 新しいQR canvas
      const options = util.qrcode_option(qrcode, cardId);
      $(elem)
        .parent()
        .parent()
        .qrcode(options);
    });
  $('#Fav_display').fadeIn();

  // 旧カツリスト、または対象弾の所持リストを表示
  // 旧カツはURLにcardImgって入ってる
  if (firstFavCardImg.indexOf('cardimg') !== -1) {
    showOldCard(); // 旧カツは弾情報が無い
  } else {
    const card = util.grepcards('cardId', firstFavCardId);
    showRegCard(card[0].versionId);
  }
}

/**
 * お気に入りグループを追加
 */
function addFavGroupItem() {
  /** 入力されたお気に入りグループ名 */
  const groupName = window.prompt('追加するお気に入りグループの名前を入力してください');
  if (!groupName || groupName === '') return;

  // お気に入り情報を生成
  const favgroupItem = {
    id: util.getNowDate(),
    name: groupName,
    favIds: []
  };

  // お気に入りグループをlocalStorageに追加
  window.FavGroup.push(favgroupItem);
  localStorage.setItem('FavGroup', JSON.stringify(window.FavGroup));

  // お気に入りグループの再描画
  const favGroupDomStr = generateFavGroupListDom(window.FavGroup);
  $('#favGroupArea').remove();
  $('.m_dress').prepend(favGroupDomStr);
}

/**
 * お気に入りグループリストのDOMを生成
 * @param {object[]} favGroup お気に入りグループ
 * @returns {string} お気に入りグループリストのDOMの文字列
 */
function generateFavGroupListDom(favGroup: Window['FavGroup']): string {
  let favGroupDomStr = '<div id="favGroupArea">';

  // ヘッダ
  favGroupDomStr += '<div class="header_label">お気に入りグループ</div>';
  // 各グループの表示
  for (const favGroupItem of favGroup) {
    favGroupDomStr += '<div class="favorite_list favorite_group">';
    favGroupDomStr += '<ul class="fav_menu fav_menu_group">';
    favGroupDomStr += `<li class="fav_group_edit" title="${favGroupItem.id}"><p class="fa fa-edit"></p></li>`;
    favGroupDomStr += `<li class="fav_group_del"  title="${favGroupItem.id}"><p class="fa fa-trash-o"></p></li>`;
    favGroupDomStr += '</ul>';
    favGroupDomStr += `<p class="p_btn btn_margin fav_group_show" title="${favGroupItem.id}">${favGroupItem.name}</p>`;
    favGroupDomStr += '</div>';
  }

  // グループが0件の時用表示
  if (favGroup.length === 0) {
    favGroupDomStr += '<div>お気に入りグループが登録されていません。<br>「グループ追加」からお気に入りグループを追加してください。</div>';
  }

  // 新規追加用の項目
  favGroupDomStr += '<div class="favorite_list favorite_group">';
  favGroupDomStr += '<ul class="fav_menu" style="background:initial;">';
  favGroupDomStr += '<li id="fav_group_add"><p class="p_btn_green">グループ追加</p></li>';
  favGroupDomStr += '</ul>';
  favGroupDomStr += '</div>';

  favGroupDomStr += '</div>';
  return favGroupDomStr;
}

/**
 * お気に入りグループ要素を削除
 * @param {string} favGroupItemId
 */
function deleteFavGroupItem(favGroupItemId: string) {
  if (confirm('お気に入りグループを削除しますか？')) {
    // 指定されたお気に入りグループIDを削除
    window.FavGroup = getFavGroup(favGroupItemId, true);
    localStorage.setItem('FavGroup', JSON.stringify(window.FavGroup));

    // お気に入りグループの再描画
    const favGroupDomStr = generateFavGroupListDom(window.FavGroup);
    $('#favGroupArea').remove();
    $('.m_dress').prepend(favGroupDomStr);
  }
}

/**
 * お気に入りグループ要素を編集
 * @param {string} favGroupItemId
 */
function showFavGroupItem(favGroupItemId: string) {
  const favGroupObject = getFavGroup(favGroupItemId)[0];
  $('.l_box .m_tit span').text(`お気に入りリスト：${favGroupObject.name}`);

  let favGroupEditDomStr = '';

  // お気に入りトップへのリンク
  favGroupEditDomStr += '<p class="p_btn btn_margin btn_link back_to_favlist_top">お気に入りトップへ</p>';
  favGroupEditDomStr += '<hr>';

  // グループ登録済みお気に入り表示ボタン
  favGroupEditDomStr += `<p id="fav_group_edit_registered" class="p_btn btn_margin btn_fav_group" data-favId="${favGroupItemId}">グループ登録済</p>`;
  // グループ未登録お気に入り表示ボタン
  favGroupEditDomStr += `<p id="fav_group_edit_not_registered" class="p_btn btn_margin btn_fav_group btn_none" data-favId="${favGroupItemId}">グループ未登録</p>`;

  // 初期化して描画
  $('.m_dress').empty();
  $('.m_dress').append(favGroupEditDomStr);

  // この画面が呼ばれた当初はグループ登録済みお気に入りを表示する
  showGroupRegisteredFavList(favGroupItemId, true);
}

/**
 * 指定されたお気に入りグループIDに従ってお気に入りリストを表示する
 * @param {string} favGroupItemId お気に入りグループID
 * @param {boolean} isRegistered true：グループ登録済みのお気に入りを表示 false：グループ未登録のお気に入りを表示
 */
function showGroupRegisteredFavList(favGroupItemId: string, isRegistered: boolean) {
  // お気に入りIDのリストを取得
  const tmpFavIdList = getFavGroup(favGroupItemId)[0].favIds;
  /** 表示対象のお気に入りIDリスト */
  let favIdList = [];
  if (isRegistered) {
    favIdList = tmpFavIdList;
  } else {
    // FVList_v2からグループに含まれていないお気に入りIDを抜き出す
    const allFavIdList = Object.keys(window.FVList_v2);
    for (const allFavId of allFavIdList) {
      if (tmpFavIdList.indexOf(allFavId) === -1) favIdList.push(allFavId);
    }
  }

  const showFavListObjectList = convertFavListToShowList(favIdList);

  let favListDomStr = '';
  // お気に入りセットのループ
  for (const showFavListObject of showFavListObjectList) {
    favListDomStr += `<div class="favorite_list" data-favid="${showFavListObject.favId}">`;

    // お気に入り操作メニュー
    // 登録済みリストの方は、グループから削除するボタンだけ表示。逆ならグループに追加するボタンを表示
    if (isRegistered) {
      favListDomStr += `<ul class="fav_menu"><li class="fav_group_item_del" data-favGroupId="${favGroupItemId}" data-favId="${
        showFavListObject.favId
      }"><p class="fa fa-minus"></p></li></ul>`;
    } else {
      favListDomStr += `<ul class="fav_menu"><li class="fav_group_item_add" data-favGroupId="${favGroupItemId}" data-favId="${
        showFavListObject.favId
      }"><p class="fa fa-plus"></p></li></ul>`;
    }

    // お気に入りセット内のドレス
    for (const oneCard of showFavListObject.cards) {
      favListDomStr += `<li class="m_dress_card"><a><div class="m_dress_card_img"><img class="lazy" src="${oneCard.altUrl}" alt="${oneCard.altName}" data-original="${
        oneCard.imageUrl
      }" data-clist="${oneCard.dataClist}"></div><div class="m_dress_card_name" data-cardId="${oneCard.cardId}" data-category="${oneCard.category}" data-qr="${oneCard.qrcode}">${
        oneCard.cardId
      }</div></a></li>`;
    }
    favListDomStr += '</div>';
  }

  if (favListDomStr === '' && isRegistered) {
    favListDomStr = '<div class="favorite_list">グループ登録済のお気に入りがありません。<br>「グループ未登録」から、好きなお気に入りを追加してください。</div>';
  }

  $('.favorite_list').remove();
  $('.m_dress').append(favListDomStr);
  startLazy();

  // ボタンのCSSを入れ替え
  if (isRegistered) {
    $('#fav_group_edit_registered').removeClass('btn_none');
    $('#fav_group_edit_not_registered').addClass('btn_none');
  } else {
    $('#fav_group_edit_registered').addClass('btn_none');
    $('#fav_group_edit_not_registered').removeClass('btn_none');
  }
}

/**
 * お気に入りグループ要素を編集 要素追加
 * @param favGroupItemId
 * @param favId
 */
function addEditFavGroupItem(favGroupItemId: string, favId: string) {
  // 要素を追加
  for (const group of window.FavGroup) {
    if (group.id === favGroupItemId) {
      group.favIds.push(favId);
      break;
    }
  }
  // 保存
  localStorage.setItem('FavGroup', JSON.stringify(window.FavGroup));

  // 画面要素から削除
  $(`.favorite_list[data-favid="${favId}"]`).remove();
}

/**
 * お気に入りグループ要素を編集 要素削除
 * @param {string} favGroupItemId
 */
function removeEditFavGroupItem(favGroupItemId: string, favId: string) {
  if (!confirm('選択したお気に入りをグループから削除しますか？')) return;

  // 要素を削除
  for (const group of window.FavGroup) {
    if (group.id === favGroupItemId) {
      for (let j = 0; j < group.favIds.length; j++) {
        if (group.favIds[j] === favId) {
          group.favIds.splice(j, 1);
          break;
        }
      }
    }
  }
  // 保存
  localStorage.setItem('FavGroup', JSON.stringify(window.FavGroup));

  // 画面要素から削除
  $(`.favorite_list[data-favid="${favId}"]`).remove();
}

/**
 * お気に入りを表示用に整形
 * @param {string[]} お気に入りIDの配列
 * @return {object[]} お気に入りリスト表示用のカードリストの配列
 */
function convertFavListToShowList(favIdList: string[]) {
  /** お気に入りリスト表示用の多次元配列 */
  const showFavListObject: FavList = [];

  for (const favId of favIdList) {
    // お気に入りセットのカードごと
    const favCards = [];
    if (!window.FVList_v2[favId]) continue;

    for (const cardIdInfo of window.FVList_v2[favId]) {
      const favCard = {
        /** カードのID */
        cardId: '',
        /** QRコード */
        qrcode: '',
        /** 部位 */
        category: '',
        /** 画像URL(フルパス) */
        imageUrl: '',
        /** アイコンのURL */
        altUrl: '',
        /** 画像のaltに相当する名前 */
        altName: '',
        /** cardId@属性@レア@部位@QR */
        dataClist: ''
      };

      if (cardIdInfo.indexOf('_') !== -1) {
        // 旧カツのカード情報
        // v1： ID_QR
        // v2： ID_QR@属性@レア@部位
        favCard.cardId = cardIdInfo.split('_')[0];
        const qrValue = cardIdInfo.split('_')[1].split('@')[0];
        const type = cardIdInfo.split('@')[1] ? cardIdInfo.split('@')[1] : '';
        const rarity = cardIdInfo.split('@')[2] ? cardIdInfo.split('@')[2] : '';
        favCard.qrcode = qrValue.indexOf('http') > -1 ? qrValue : `${CONFIG.url.aikt}qr/id=${qrValue}`;
        favCard.category = cardIdInfo.split('@')[3] ? cardIdInfo.split('@')[3] : '';
        favCard.imageUrl = `${CONFIG.url.aiktCardImg}${favCard.cardId}.jpg`;
        favCard.altUrl = CONFIG.CATEGORY_ICON_URL[favCard.category.toLowerCase()];
        favCard.altName = favCard.cardId; // 旧カツは名前が取れないのでカードIDをそのまま入れる
        favCard.dataClist = `${favCard.cardId}@${type}@${rarity}@${favCard.category}@${qrValue}`;
      } else {
        // スターズ以降のカード情報
        favCard.cardId = cardIdInfo;
        const card = util.grepcards('cardId', favCard.cardId);
        if (card.length !== 0) {
          switch (util.getAikatsuVersion(card[0].versionId)) {
            case 'stars':
            case 'friends':
            case 'onparade':
              favCard.imageUrl = makeFullImageUrl(card[0].versionId, card[0].imageUrl);
              break;
          }
          favCard.qrcode = card[0].qrcode;
          favCard.category = card[0].category;
          favCard.altUrl = CONFIG.CATEGORY_ICON_URL[card[0].category.toLowerCase()];
          favCard.altName = card[0].cardName;
          favCard.dataClist = `${favCard.cardId}@${card[0].type}@${card[0].rarity}@${card[0].category}@${card[0].qrcode}`;
        } else {
          // カードIDに合致する情報が取れなかった時
          favCard.qrcode = '';
          favCard.category = '';
          favCard.altUrl = '';
          favCard.altName = 'カード情報が取得できませんでした';
          favCard.dataClist = '';
        }
      }
      // 1カード分
      favCards.push(favCard);
    }
    // 1セット分
    showFavListObject.push({
      favId,
      cards: favCards
    });
  }
  return showFavListObject;
}

/**
 * DOMのstring返す
 * @param {object[]} showFavListObjectList
 * @return {string}
 */
function generateFavListDom(showFavListObjectList: FavList): string {
  let favListDomStr = '';

  // お気に入りセットのループ
  for (const showFavListObject of showFavListObjectList) {
    favListDomStr += `<div class="favorite_list" data-favid="${showFavListObject.favId}">`;
    favListDomStr += `<ul class="fav_menu"><li class="fav_del" title="${
      showFavListObject.favId
    }"><p class="fa fa-trash-o"></p></li><li class="fav_edit"><p class="fa fa-edit"></p></li></ul>`;
    // お気に入りセット内のドレスのループ
    for (const oneCard of showFavListObject.cards) {
      favListDomStr += `<li class="m_dress_card"><a><div class="m_dress_card_img"><img class="lazy" src="${oneCard.altUrl}" alt="${oneCard.altName}" data-original="${
        oneCard.imageUrl
      }" data-clist="${oneCard.dataClist}"></div><div class="m_dress_card_name" data-cardId="${oneCard.cardId}" data-category="${oneCard.category}" data-qr="${oneCard.qrcode}">${
        oneCard.cardId
      }</div></a></li>`;
    }
    favListDomStr += '</div>';
  }
  return favListDomStr;
}

/**
 * お気に入りグループから、指定されたIDに従って要素を取得
 * @param favGroupItemId お気に入りグループID
 * @param invert true: 指定したID以外のものを返す
 * @return 合致するお気に入りグループのリスト
 */
function getFavGroup(favGroupItemId: string, invert = false) {
  return $.grep(
    window.FavGroup,
    item => {
      return item.id === favGroupItemId;
    },
    invert
  );
}
