// 統計処理
import util from './util';
import CONFIG from './config';
import { Card } from '../types/common';
import { makeRequestUrl } from './util/common';

/**
 * スクリプト起動時の統計処理
 */
export function calcStatInit() {
  // プロモはカード追加になってる可能性が高いので必ず対象にする
  let versionIds = ['458801', '458901', '391801'];

  // 統計が未処理の弾に絞り込み
  const tmp = localStorage.getItem('STATISTICS');
  const stat: Window['STATISTICS'] = tmp ? JSON.parse(tmp) : {};

  window.SERIES.map(series => {
    if (!stat[series.id]) {
      versionIds.push(series.id);
    }
  });
  versionIds = [...new Set(versionIds)];
  calcStatSpecific(versionIds);
}

/**
 * 全ての弾の統計処理
 */
export function calcStatAll() {
  const versionIds: string[] = [];
  window.SERIES.map(series => {
    versionIds.push(series.id);
  });
  calcStatSpecific(versionIds);
}

/**
 * 指定された弾の統計処理
 * @param versionIds 弾の配列
 */
export async function calcStatSpecific(versionIds: string[]) {
  // 所持枚数集計
  const collectCount = getCollectCount();

  for (const versionId of versionIds) {
    const aikatsuVer = util.getAikatsuVersion(versionId);
    let url: string = '';
    switch (aikatsuVer) {
      case 'stars':
        url = `${CONFIG.url.aist}cardlist/dresslist.php?search=true&category=${versionId}`;
        break;
      case 'friends':
        url = `${CONFIG.url.aifr}cardlist/?search=true&category=${versionId}`;
        break;
      case 'onparade':
        url = `${CONFIG.url.aiop}cardlist/?search=true&category=${versionId}`;
        break;
      default:
        continue;
    }

    try {
      // ある弾の全体の枚数を取得する
      let data = await (await fetch(makeRequestUrl(url))).text();
      // 該当弾の統計情報の初期化
      window.STATISTICS[versionId] = {
        id: versionId,
        all: {
          type: {},
          rarity: {}
        },
        collect: collectCount[versionId],
        rate: {
          type: {},
          rarity: {}
        }
      };
      CONFIG.TYPELIST.forEach(type => {
        window.STATISTICS[versionId].all.type[type] = 0;
        window.STATISTICS[versionId].rate.type[type] = 0;
      });
      CONFIG.RARITYLIST.forEach(rarity => {
        window.STATISTICS[versionId].all.rarity[rarity] = 0;
        window.STATISTICS[versionId].rate.rarity[rarity] = 0;
      });
      // console.log(aikatsuVer);
      switch (aikatsuVer) {
        case 'stars': {
          // 404対策で文字列検索で取得する
          const e = data.match(/key=\w*/g);
          if (!e) continue;
          $.each(e, i => {
            const rarityType = util.uniqueImageUrl(e[i]);
            window.STATISTICS[versionId].all.type[rarityType.type as string] += 1;
            window.STATISTICS[versionId].all.rarity[rarityType.rarity as string] += 1;
          });
          break;
        }
        case 'friends':
        case 'onparade':
          // 画像にアクセスしないようにダミー情報で上書き
          data = data.replace(/img src=".+?"/g, 'img src="../images/cardlist/card/000000/ "');
          $(data)
            .find('#lists > .data')
            .each((index, cardDetail) => {
              const cardInfo = util.extractCardInfoFriends(cardDetail.outerHTML);
              // console.log(cardInfo);
              window.STATISTICS[versionId].all.type[cardInfo.card.type as string] += 1;
              window.STATISTICS[versionId].all.rarity[cardInfo.card.rarity as string] += 1;
            });
          break;
        default:
          break;
      }

      // 属性の収集率を計算
      CONFIG.TYPELIST.forEach(type => {
        const tmp = Math.round((window.STATISTICS[versionId].collect.type[type] / window.STATISTICS[versionId].all.type[type]) * 100);
        window.STATISTICS[versionId].rate.type[type] = Number(
          String(tmp)
            .replace('NaN', '0')
            .replace('Infinity', '0')
        );
      });

      // レアリティの収集率を計算
      CONFIG.RARITYLIST.forEach(rarity => {
        const tmp = Math.round((window.STATISTICS[versionId].collect.rarity[rarity] / window.STATISTICS[versionId].all.rarity[rarity]) * 100);
        window.STATISTICS[versionId].rate.rarity[rarity] = Number(
          String(tmp)
            .replace('NaN', '0')
            .replace('Infinity', '0')
        );
      });

      // 更新したのでLocalStorageに保存する
      localStorage.setItem('STATISTICS', JSON.stringify(window.STATISTICS));
    } catch (e) {
      console.log('統計情報収集中にエラー');
      console.log(e);
    }
  }
}

/**
 * 表示中の弾の収集枚数を計算する
 * 1. 表示中の全てのカードを取得
 * 2. 属性、レアリティごとにカウント
 * @return {Object} 表示中の弾の収集枚数
 */
function getCollectCount() {
  // 重複したIDを削除する
  let tmpList: Card[] = JSON.parse(JSON.stringify(window.RGList_v2));
  // IDの+とかを削除する
  tmpList.forEach((card: Card) => {
    // +とかついてない素のIDの生成
    card.originalId = card.cardId.replace('+', '');
  });

  // originalIdをkeyとしたオブジェクトに格納する。
  // +つき、+なしなどはこれでマージされる。
  const tmpObj: { [id: string]: Card } = {};
  tmpList.forEach(card => {
    tmpObj[card.originalId as string] = card;
  });
  // 配列に戻す。
  tmpList = [];
  const keys = Object.keys(tmpObj);
  for (const key of keys) {
    tmpList.push(tmpObj[key]);
  }

  const stat = {};
  window.SERIES.forEach(series => {
    // 収集枚数初期化
    stat[series.id] = {
      type: {},
      rarity: {}
    };

    // 該当弾のカードを抜き出す
    const oneVerList = $.grep<Card>(tmpList, card => {
      return card.versionId === series.id;
    });
    // 属性
    CONFIG.TYPELIST.forEach(type => {
      stat[series.id].type[type] = $.grep(oneVerList, card => {
        return card.type === type;
      }).length;
    });
    // レアリティ
    CONFIG.RARITYLIST.forEach(rarity => {
      stat[series.id].rarity[rarity] = $.grep(oneVerList, card => {
        return card.rarity === rarity;
      }).length;
    });
  });

  return stat;
}
