import util from './util';
import CONFIG from './config';
import { drawCards } from './cardView';
import { msgAlert } from './util/view';

/**
 * メニュー：旧アイカツカード
 * 1. akdbからCSVを取得
 * 2. CSVをスマートバインダーの形式に解釈し、カードを描画
 * @param  {String} showSeries 弾ID
 */
export function showOldCard(showSeries?: string) {
  util.loader();
  try {
    // 弾指定が無い場合は弾選択メニューを再描画して、最新の弾を描画する
    if (!showSeries) {
      $('.m_select option').remove();
      $('.m_select>select').append(
        CONFIG.OLDSERIES.map(series => {
          return `<option value="${series.id}">${series.name}</option>`;
        }).join('')
      );
      $('.m_select').show();

      // 選択中の弾を取得
      showSeries = $('.m_select>select option:selected').val() as string;
      // フィルターを表示
      $('.m_btn_filter').show();
      // タイトルをセット
      $('.l_box .m_tit span').text('旧アイカツカード');
    }
    util.nav();
    // 各領域の初期化
    $('.favorite_list,.prom_area,.description,.delall').remove();
    $('.m_card_area').hide();
    $('.m_dress').empty();

    // 既に取得済みのデータを確認し、無ければgetする
    let cardList = util.grepOldCards('versionId', showSeries);
    if (cardList.length === 0) {
      util
        .getOldCard(showSeries)
        .then(getCardList => {
          window.OldList = [...window.OldList, ...getCardList];
          cardList = getCardList;

          // 表示弾のカードリストをグローバル変数に格納
          window.selectedCardList = cardList;
          // フィルター処理
          cardList = util.filter(window.selectedCardList);
          // カード描画
          drawCards(cardList);
          // 後処理
          util.loadend();
        })
        .catch(error => {
          console.log(error);
          util.loadend();
        });
    } else {
      // 表示弾のカードリストをグローバル変数に格納
      window.selectedCardList = cardList;
      // フィルター処理
      cardList = util.filter(window.selectedCardList);
      // カード描画
      drawCards(cardList);
      // 後処理
      util.loadend();
    }
  } catch (e) {
    msgAlert('エラーが発生しました');
    util.loadend();
    console.log(e);
  }
}

/**
 * メニュー：所持リスト
 * @param series 弾ID
 */
export function showRegCard(series?: string) {
  util.loader();
  // 弾指定が無い場合は弾選択メニューを再描画して、最新の弾を描画する
  if (!series || $('.m_select').is(':hidden')) {
    $('.m_select option').remove();
    window.SERIES.forEach(v => {
      $('.m_select select').append('<option value="' + v.id + '">' + v.name + '</option>');
    });
    $('.m_select').show();
    // 選択中の弾を取得
    if (!series) {
      series = $('.m_select>select option:selected').val() as string;
    } else {
      $('.m_select select').val(series);
    }
    // フィルターを表示
    $('.m_btn_filter').show();
    // タイトルをセット
    $('.l_box .m_tit span').text('所持リスト');
  }
  util.nav();
  $('.m_card_area').hide();
  $('.favorite_list,.prom_area,.description,.delall').remove();
  $('.m_dress').empty();
  $('.m_dress').append('<div class="description"><small>※ドレス長押しで所持リストから削除します。</small><small class="delall">一括削除</small></div>');
  // peger
  $('.m_dress').append('<div class="pager"></div>');
  $('.m_dress').append('<div class="total"></div>');

  // 選択中の弾のカードリスト
  window.selectedCardList = util.grepcards('versionId', series);
  // フィルタ処理
  const cardList = util.filter(window.selectedCardList);
  // カード描画
  drawCards(cardList);
  // ページネーション
  util.pagenation();
  // 収集率表示
  cyclebar(series);
  // 後処理
  util.loadend();
}

/**
 * 収集率の円グラフ表示切替
 * @param  {String} versionId 弾ID
 */
function cyclebar(versionId: string) {
  // 属性の円グラフをクリックした時
  $(document).on('click', '.circleType', () => {
    $('.circleType').hide();
    $('.circleRate').show();
    $('.pie_prg').asPieProgress('reset');
    $('.pie_prg').asPieProgress('start');
  });
  // レアリティの円グラフをクリックした時
  $(document).on('click', '.circleRate', () => {
    $('.circleRate').hide();
    $('.circleType').show();
    $('.pie_prg').asPieProgress('reset');
    $('.pie_prg').asPieProgress('start');
  });

  // 統計情報がある場合、収集率の円グラフを描画
  const stat = window.STATISTICS[versionId];
  if (stat) {
    drawCircle(stat, false);
  }

  // 属性の収集率を非表示にする
  $('.circleType').hide();

  // ゲージを動かす
  $('.pie_prg').asPieProgress('start');
}

/**
 * 収集率の円グラフを描画
 * 統計画面の場合はフラグを指定
 * @param  {Object}  stat   収集率
 * @param  {Boolean} isStat true:統計画面 false:それ以外
 */
function drawCircle(stat: Window['STATISTICS'][0], isStat: boolean) {
  let htmlStr = '';
  if (isStat) {
    htmlStr += '<div class="circleType2">';
  } else {
    htmlStr += '<div class="circleType">';
  }
  CONFIG.TYPELIST.forEach(v => {
    const rate = stat.rate.type[v] ? stat.rate.type[v] : 0;
    const collect = stat.collect.type[v] ? stat.collect.type[v] : 0;
    const all = stat.all.type[v] ? stat.all.type[v] : 0;
    htmlStr +=
      `<div class="pie_${v}" data-goal="${rate}" data-all="${all}">` + '  <span class="pie_prg__number">0%</span>' + `  <span class="pie_lb">${collect}/${all}</span>` + '</div>';
  });
  htmlStr += '</div>';

  if (isStat) {
    htmlStr += '<div class="circleRate2">';
  } else {
    htmlStr += '<div class="circleRate">';
  }
  CONFIG.RARITYLIST.forEach(v => {
    const rate = stat.rate.rarity[v] ? stat.rate.rarity[v] : 0;
    const collect = stat.collect.rarity[v] ? stat.collect.rarity[v] : 0;
    const all = stat.all.rarity[v] ? stat.all.rarity[v] : 0;
    htmlStr +=
      ` <div class="pie_${v}" data-goal="${rate}" data-all="${all}">` + '  <span class="pie_prg__number">0%</span>' + `  <span class="pie_lb">${collect}/${all}</span>` + '</div>';
  });

  htmlStr += '</div>';

  if (isStat) {
    $(`.stat [name="stat${stat.id}"]`).append(htmlStr);
  } else {
    $('.m_dress').prepend(htmlStr);
  }

  /** 収集率円グラフの共通パラメータ */
  const progressParam = {
    namespace: 'pie_prg',
    barcolor: '',
    size: 40,
    speed: 10
  };

  $('.pie_qt').asPieProgress({
    ...progressParam,
    barcolor: '#FAA'
  });
  $('.pie_co').asPieProgress({
    ...progressParam,
    barcolor: '#6AF'
  });
  $('.pie_se').asPieProgress({
    ...progressParam,
    barcolor: '#A9E'
  });
  $('.pie_po').asPieProgress({
    ...progressParam,
    barcolor: '#FB8'
  });

  $('.pie_lpr').asPieProgress({
    ...progressParam,
    barcolor: '#EB0'
  });

  $('.pie_jlr').asPieProgress({
    ...progressParam,
    barcolor: '#EB0'
  });

  $('.pie_bfr').asPieProgress({
    ...progressParam,
    barcolor: '#EB0'
  });
  $('.pie_fr').asPieProgress({
    ...progressParam,
    barcolor: '#CCC'
  });
  $('.pie_cr').asPieProgress({
    ...progressParam,
    barcolor: '#7EC'
  });
  $('.pie_pr').asPieProgress({
    ...progressParam,
    barcolor: '#EB0'
  });
  $('.pie_r').asPieProgress({
    ...progressParam,
    barcolor: '#CCC'
  });
  $('.pie_n').asPieProgress({
    ...progressParam,
    barcolor: '#C66'
  });
  $('.pie_prg').each(function() {
    // 公式の合計が0枚のものは非表示にする
    // data-goalを参照すると、所持数0も非表示になるので注意
    if ($(this).attr('data-all') === '0') {
      $(this)
        .find('.pie_prg__number')
        .hide();
      $(this)
        .find('.pie_lb')
        .hide();
    }
    // コンプリートメダルをセット
    if ($(this).attr('data-goal') === '100') {
      $(this).prepend('<img src="/images/binder/img_medal-comp.png" class="comp">');
    }
  });
}
