/**
 * メッセージ表示
 * @param  {String} keyword 表示したいメッセージ
 */
export function msgAlert(keyword: string) {
  $('.alert').hide();
  $('.alert').text(keyword);
  $('.alert').stop(true, true);
  $('.alert')
    .fadeIn(500)
    .delay(2000)
    .fadeOut(2000);
}

/**
 *  表示中のカードに対してLazyLoadを起動する。
 */
export function startLazy() {
  // イベントを削除
  $('.m_dress').off('scroll');

  // Lazy Loadの起動
  $('img.lazy').lazyload({
    container: $('.m_dress'),
    threshold: 500, // XXXpxの距離まで近づいたら表示する
    // effect: 'fadeIn', // じわじわっと表示させる
    // effect_speed: 100, // Xミリ秒かけて表示させる
    event: 'scroll', // 表示のトリガーとなるイベント名。defaultはscroll
    failure_limit: 30,
    skip_invisible: true
  });
}
