import CONFIG from '../config';
import * as fetchJsonp from 'fetch-jsonp';
import * as CryptoJS from 'crypto-js';

/**
 * 乱数生成
 * @param  n 乱数の長さ
 * @return 乱数
 */
export function create_privateid(n: number): string {
  const CODE_TABLE = '123456789ABCDEFGHJKLMNPQRSTUVWXYZabcdefghijkmnopqrstuvwxyz';
  let r = '';
  for (let i = 0, k = CODE_TABLE.length; i < n; i++) {
    r += CODE_TABLE.charAt(Math.floor(k * Math.random()));
  }
  return r;
}

/**
 * CORS対応のGET
 * @param url
 * @param api
 * @param free
 */
export function searchPromo(url: string, api: string, free: string): Promise<string> {
  return new Promise((resolve, reject) => {
    fetchJsonp(`https://script.google.com/macros/s/AKfycbz2aZfc8mktKaabkLBtt58arxmKYmyLR6Kq8xJONZxAuA9BiEyC/exec?url=${url}&api=${api}&free=${free}`, {
      jsonpCallback: 'callback'
    })
      .then((response: any) => {
        return response.json();
      })
      .then((json: any) => {
        resolve(json.htmlStr);
      })
      .catch((error: any) => {
        reject({ error });
      });
  });
}

/**
 * CORS対応URLを生成する
 * @param value アクセス先URL
 * @return CORS対応URL
 */
export function makeRequestUrl(value: string): string {
  const result = CONFIG.url.akdbAjax + encodeURIComponent(value);
  return result;
}

/**
 * JavaScriptでHTMLエスケープする
 * @param str エスケープ対象の文字列
 * @return HTMLエスケープした文字列
 */
export function escape_html(str: string): string {
  if (typeof str !== 'string') {
    return str;
  }
  return str.replace(/[&'`"<>]/g, match => {
    return {
      '&': '&amp;',
      '\'': '&#x27;',
      '`': '&#x60;',
      '"': '&quot;',
      '<': '&lt;',
      '>': '&gt;'
    }[match];
  });
}

/**
 * Ajaxで取得した時に、相対パスになっていて404になる箇所を、http://～を補填して、絶対パスにする
 * @param responseText ajaxで取得したHTMLのテキスト
 * @return 絶対パスに置換したHTMLのテキスト
 */
export function UrlRelatetoAbsolute(responseText: string): string {
  responseText = responseText.replace(/\.\.\/images/g, 'http://www.aikatsu.com/stars/images');
  responseText = responseText.replace(/src="\//g, 'src="http://www.aikatsu.com/');
  return responseText;
}

/**
 * localStorageからQRを読み出し
 */
export function ReadLocalStorage() {
  const rg = localStorage.getItem('RGList_v2');
  if (rg) window.RGList_v2 = JSON.parse(rg);

  const fv = localStorage.getItem('FVList_v2');
  if (fv) window.FVList_v2 = JSON.parse(fv);

  const idList = localStorage.getItem('IDList');
  if (idList) window.IDList = JSON.parse(idList);

  const gplist = localStorage.getItem('GPList');
  if (gplist) window.GPList = JSON.parse(gplist);

  const stat = localStorage.getItem('STATISTICS');
  if (stat) window.STATISTICS = JSON.parse(stat);

  const fav = localStorage.getItem('FavGroup');
  if (fav) window.FavGroup = JSON.parse(fav);
}

/**
 * 文字列の暗号化
 * @param  key  暗号化キー
 * @param  text 暗号化したい文字列
 * @return 暗号化した文字列
 */
export function crypto(key: string, text: string): string {
  const utf8 = CryptoJS.enc.Utf8.parse(text);
  const enctext = CryptoJS.AES.encrypt(utf8, key);
  return enctext.toString();
}

/**
 * 文字列の復号化
 * @param  key     復号化キー
 * @param  enctext 復号化したい文字列
 * @return 復号化した文字列
 */
export function decrypto(key: string, enctext: string): string | null {
  const decrypted = CryptoJS.AES.decrypt(enctext, key);
  let dectext = null;
  if (decrypted.toString().length > 100) {
    dectext = decrypted.toString(CryptoJS.enc.Utf8);
  }
  return dectext;
}

/**
 * グループ共有除外弾か判定する
 * @param versionId 弾ID
 * @return true:除外 false:除外じゃない
 */
export function isExcluedGroupSyncVersion(versionId: string): boolean {
  const GrpSyncIgnoreVersion = localStorage.getItem('GrpSyncIgnoreVersion');
  if (!GrpSyncIgnoreVersion) return false;
  if (GrpSyncIgnoreVersion.indexOf(versionId) > -1) {
    return true;
  }
  return false;
}

