/**
 * 表示中のカードにページネーションを適用
 */
export function pagenation() {
  $('.pager').empty();
  $('.total').empty();
  let num = 0;
  let page = 0;
  /** 1ページの表示数 */
  const view = 99; // ここはユーザーが決められるようにしても良いか

  // 表示された所持リストを回す
  let nowPage = 1;
  for (const elem of $('.m_inner .m_dress_card')) {
    // フィルタによる表示、非表示を判断し、非表示ならスキップ
    if ($(elem).is(':hidden')) continue;
    num = num + 1;

    // 1ページの表示枚数を超えたらページ番号をインクリメント
    if (num > view * nowPage) nowPage++;

    // ここ最適化したい
    // 1ページ目はデフォルトで表示。2ページ目以降は非表示。
    $(elem).attr('pager', nowPage);
    if (nowPage !== 1) $(elem).hide();
  }

  if (num > view) {
    // view件以下はページャーを表示しない
    page = Math.ceil(num / view);
    // ページ数を表示
    for (let i = 1; i <= page; i++) {
      $('.pager').append('<li class="page_num">' + i + '</li>');
    }
    // 1ページ目をアクティブにする
    $('.pager li:eq(0)').addClass('active');
  }
  $('.total').text('表示ドレス:' + num);
}

/**
 * ImageUrlから属性とレアリティを返す
 * @param imageUrl スターズの画像ファイル名の文字列
 * @return レアリティと属性のオブジェクト
 */
export function uniqueImageUrl(imageUrl: string) {
  const col: {
    type: string | null;
    rarity: string | null;
  } = {
    type: '',
    rarity: ''
  };
  // var rarity = { "レア": "r", "プレミアムレア": "pr", "スタープレミアムレア": "pr", "レア": "r", "ノーマル": "n" };
  const type = imageUrl.toLowerCase().match(/(_qt|_co|_se|_po)/);
  if (type) {
    col.type = type[0].replace('_', '');
  } else if (imageUrl.toLowerCase().match(/_top_|_sho_/)) {
    // P S-100、P S-101用
    col.type = 'qt';
  } else if (imageUrl.toLowerCase().match(/(qt|co|se|po)/)) {
    // P 136-★-★ P14_W003Qt、P 137-★-★ P14_S041Qt
    col.type = imageUrl.toLowerCase().match(/(qt|co|se|po)/)![0];
  } else if (imageUrl.toLowerCase().match(/np07_h5_pr_sun_001_.01/)) {
    // 太陽のドレス
    col.type = 'qt';
  } else if (imageUrl.toLowerCase().match(/np07_h5_pr_sun_001_.02/)) {
    // 太陽のドレス
    col.type = 'co';
  } else if (imageUrl.toLowerCase().match(/np07_h5_pr_sun_001_.03/)) {
    // 太陽のドレス
    col.type = 'co';
  } else if (imageUrl.toLowerCase().match(/np07_h5_pr_sun_001_.04/)) {
    // 太陽のドレス
    col.type = 'se';
  } else if (imageUrl.toLowerCase().match(/np07_h5_pr_sun_001_.05/)) {
    // 太陽のドレス
    col.type = 'se';
  } else if (imageUrl.toLowerCase().match(/np07_h5_pr_sun_001_.06/)) {
    // 太陽のドレス
    col.type = 'po';
  } else if (imageUrl.toLowerCase().match(/np07_h6_pr_moon_001_.01/)) {
    // 月のドレス
    col.type = 'qt';
  } else if (imageUrl.toLowerCase().match(/np07_h5_pr_sun_001_.07/)) {
    // 太陽のドレス
    col.type = 'qt';
  } else if (imageUrl.toLowerCase().match(/np07_h5_pr_sun_001_.08/)) {
    // 太陽のドレス
    col.type = 'co';
  } else if (imageUrl.toLowerCase().match(/np07_h5_pr_sun_001_.09/)) {
    // 太陽のドレス
    col.type = 'po';
  } else {
    col.type = null;
  }

  const r = imageUrl.toLowerCase().match(/(_pr|_cr|_r|_n)/);
  // 特殊なレアリティが設定されている画像に対しては手動でレアリティをセットする。matchする文字列は小文字
  // URLからレアリティを取得できるが、実際のレアリティが違うケースは先にチェックする。
  if (imageUrl.toLowerCase().match(/np04_p04_qt_n001_wsa_/)) {
    col.rarity = 'r';
  } else if (imageUrl.toLowerCase().match(/np08_p0_exa_qt_001_a01/)) {
    col.rarity = 'r';
  } else if (imageUrl.toLowerCase().match(/np08_p0_exa_co_001_a01/)) {
    col.rarity = 'r';
  } else if (imageUrl.toLowerCase().match(/np08_p0_exa_se_001_a01/)) {
    col.rarity = 'r';
  } else if (imageUrl.toLowerCase().match(/np04_p04_se_n002_tbs_/)) {
    col.rarity = 'cr';
  } else if (r) {
    col.rarity = r[0].replace('_', '');
  } else if (imageUrl.toLowerCase().match(/exd/)) {
    col.rarity = 'r';
  } else if (imageUrl.toLowerCase().match(/exa/)) {
    col.rarity = 'n';
  } else if (imageUrl.toLowerCase().match(/np07_p0_1st/)) {
    col.rarity = 'cr';
  } else if (imageUrl.toLowerCase().match(/np07_p0_2nd/)) {
    col.rarity = 'cr';
  } else if (imageUrl.toLowerCase().match(/school/)) {
    col.rarity = 'n';
  } else if (imageUrl.toLowerCase().match(/style/)) {
    col.rarity = 'r';
  } else if (imageUrl.toLowerCase().match(/collabo/)) {
    col.rarity = 'cr';
  } else if (imageUrl.toLowerCase().match(/uniform/)) {
    col.rarity = 'n';
  } else if (imageUrl.toLowerCase().match(/unique/)) {
    col.rarity = 'r';
  } else if (imageUrl.toLowerCase().match(/poke/)) {
    col.rarity = 'r';
  } else if (imageUrl.toLowerCase().match(/top_1063|sho_1057|sho_1244|top_1244|sho_1211|top_1198/)) {
    // ブロマイド
    col.rarity = 'n';
  } else if (imageUrl.toLowerCase().match(/p14_w003qt|p14_s041qt/)) {
    // スノーイリュージョン
    col.rarity = 'r';
  } else {
    // 全て一致しない場合はrarityをnullにする
    col.rarity = null;
  }
  // console.log(`[${imageUrl}][レア]${col.rarity}[属性]${col.type}`);
  return col;
}

/**
 * 表示中の弾が旧アイカツカードか判定する
 * @return true:旧アイカツ false:スターズ
 */
export function isOldCardList(): boolean {
  const label = $('.m_tit > span').text();
  if (label === '旧アイカツカード') {
    return true;
  }
  return false;
}

/**
 * QRコード生成のオプション
 * @param c QRコード
 * @param n カードID
 * @return QRコード用のオプションオブジェクト
 */
export function qrcode_option(c: string, n: string) {
  c = c.trim();
  const options = {
    render: 'canvas',
    ecLevel: 'L',
    minVersion: 3,
    fill: '#000000',
    background: '#ffffff',
    text: c,
    size: 250,
    quiet: 1,
    label: n,
    mSize: 0.1,
    mPosX: 0.5,
    mPosY: 0.5,
    fontname: 'ubuntu',
    fontcolor: '#000000'
  };
  return options;
}

/**
 * 年月日の文字列を返却する
 * @return 年月日(YYYYMMDDHHmmSS)
 */
export function getNowDate(): string {
  const now = new Date();
  const y = now.getFullYear();
  let m = (now.getMonth() + 1).toString();
  let d = now.getDate().toString();
  let hour = now.getHours().toString();
  let minute = now.getMinutes().toString();
  let second = now.getSeconds().toString();
  m = ('0' + m).slice(-2);
  d = ('0' + d).slice(-2);
  hour = ('0' + hour).slice(-2);
  minute = ('0' + minute).slice(-2);
  second = ('0' + second).slice(-2);

  return `${y}${m}${d}${hour}${minute}${second}`;
}

export const convertStrToCsv = (data: string): string[][] => {
    const result:string[][] = [];
    const lines = data.split(/\n|\r\n/g);
    for(const line of lines){
      if(!line) continue;
      result.push(line.split(','))
    }
    return result;
};
